<template>
	<div class="mygoods">
		<el-button type="primary">商品列表</el-button>
		<el-button type="success" @click='addmygoods'>新增商品</el-button>
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="re_goods_name" label="商品名称" width="180">
			</el-table-column>
			<el-table-column prop="re_goods_price" label="价格" width="180">
			</el-table-column>
			<el-table-column prop="re_goods_stock" label="库存">
			</el-table-column>
			<el-table-column prop="re_goods_picture" label="主图">
				<template slot-scope="scope">
					<el-image style="width: 100px; height: 100px" :src="imgurls + scope.row.re_goods_picture" :preview-src-list='[imgurls + scope.row.re_goods_picture+""]'
					 fit="fill"></el-image>
				</template>
			</el-table-column>
			<el-table-column prop="re_goods_introduction" label="商品简介">
			</el-table-column>
			<el-table-column prop="re_production_time" label="生产时间">
			</el-table-column>
			<el-table-column prop="re_expiration_time" label="过期时间">
			</el-table-column>
			<el-table-column prop="is_distribution" label="是否开启分销">
				<template slot-scope="scope">
					<el-button type="warning" @click='switchs(scope.row)'>{{scope.row.is_distribution == 0 ? '开启' : '关闭'}}</el-button>
				</template>
			</el-table-column>
			<el-table-column prop="" label="操作" width='280'>
				<template slot-scope="scope">
					<el-button type="primary" @click='exgoods(scope.row)'>修改商品</el-button>
					<el-button type="danger" @click='delgoods(scope.row,scope.$index)'>删除商品</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [],
				currentPage: 0,
				total: 0,
				pagesize: 0,
				imgurls:this.global.imgurl,
			}
		},
		methods: {
			delgoods(e,a){
				 this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
							this.utils.post({
								url:'admin_reseller_goods_delete',
								data:{
									re_goods_id:e.re_goods_id,
									shop_id:this.global.shop_id()
								}
							}).then(res=>{
								this.tableData.splice(a,1)
							})
							
							
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消删除'
				          });          
				        });
			},
			
			
			exgoods(e){
				this.$router.push({
					path:"/addmygoods",
					query:{
						goodsmsg:e,
						run:'exc'
					}
				})
			},
			
			// 是否开启分销
			switchs(e) {
				if (e.is_distribution == 0) {
					let sw = 1
					this.isshopopen(sw, e)
				} else if (e.is_distribution == 1) {
					let sw = 0
					this.isshopopen(sw, e)
				}
			},

			isshopopen(sw, e) {
				this.utils.post({
					url: 'admin_reseller_goods',
					data: {
						goods_reseller: sw,
						re_goods_id: e.re_goods_id,
						shop_id: e.shop_id
					}
				}).then(res => {
					this.tableData.forEach((item,index)=>{
						if(item.re_goods_id == e.re_goods_id ){
							item.is_distribution = sw
						}
					})
				})
			},

			addmygoods() {
				this.$router.push({
					path: '/addmygoods',
					query: {
						addmygoods: true,
						run:'add'
					}
				})
			},
			init() {
				this.utils.post({
					url: 'admin_reseller_goods_list',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {

					res.data.forEach((item, index) => {
						item.re_production_time = this.global.timelaver(item.re_production_time * 1000)
						item.re_expiration_time = this.global.timelaver(item.re_expiration_time * 1000)
						// item.is_distribution = item.is_distribution == 0 ? false : true
					})
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					this.tableData = res.data
					// re_production_time
					// timelaver
				})
			},
			changepages(e){
				this.utils.post({
					url: 'admin_reseller_goods_list',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => { 
					res.data.forEach((item, index) => {
						item.re_production_time = this.global.timelaver(item.re_production_time * 1000)
						item.re_expiration_time = this.global.timelaver(item.re_expiration_time * 1000)
						// item.is_distribution = item.is_distribution == 0 ? false : true
					})
					
					this.tableData = res.data
				})
			}

		},
		mounted() {
			this.init()
		}
	}
</script>

<style>
</style>
