<template>
	<div class="addgoods">
		<div v-if='isadd'>新增服务</div>
		<div v-else>修改服务</div>
		<div style="margin: 20px;"></div>
		<el-form :label-position="labelPosition" label-width="130px" :model="formLabelAlign">
			<el-form-item label="服务分类" v-if='isadd'>
				<el-select v-model="value" placeholder="请选择" @change='changsome'>
					<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="服务名称">
				<el-input class='sty' v-model="formLabelAlign.goods_name"></el-input>
			</el-form-item>
			<el-form-item label="服务价格">
				<el-input class='sty' type='number' v-model="formLabelAlign.price"></el-input>
			</el-form-item>
			<el-form-item label="服务功效">
				<el-input class='sty' v-model="formLabelAlign.goods_effect"></el-input>
			</el-form-item>

			<el-form-item label="服务轮播" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="uploada" :data='a' list-type="picture-card" :limit='4' :file-list="fileList1"
				 :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwlbcg' :multiple='true'>
					<i class="el-icon-plus"></i>
					<el-button class='mysome' style="margin-left: 10px; display: none;" size="small" type="success" @click="submitUploada">上传到服务器</el-button>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item>

			<!-- 服务轮播修改页面 -->
			<el-form-item label="服务轮播" v-if='!isadd'>
				<div class="any-flex">
					<div v-for='(item,index) in fwlbt' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]  ' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>




			<el-form-item label="服务主图1" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="uploadb" :data='b' list-type="picture-card" :limit='1' :file-list="fileList1"
				 :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwzt1cg' :multiple='false'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item>

			<!--服务主图修改页面 -->
			<el-form-item label="服务主图1" v-if='!isadd'>
				<div class="any-flex">
					<div v-for='(item,index) in fwzttp1' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess2'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>

			<!-- 			<el-form-item label="服务主图2" v-if='isadd'>
				<el-upload class='uplodes' action="upload" :data='c' ref="uploadc" list-type="picture-card" :limit='1' :file-list="fileList1"
				 :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwzt2cg' :multiple='false'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item> -->

			<!--服务主图修改页面 -->
			<!-- 			<el-form-item label="服务主图2" v-if='!isadd'>
				<div class="any-flex">
					<div v-for='(item,index) in fwzttp2' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
						
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess3'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item> -->

			<!-- 			<el-form-item label="服务主图3" v-if='isadd'>
				<el-upload class='uplodes' action="upload" :data='d' ref="uploadd" list-type="picture-card" :limit='1' :file-list="fileList1"
				 :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwzt3cg' :multiple='false'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item> -->

			<!--服务主图修改页面 -->
			<!-- 			<el-form-item label="服务主图3" v-if='!isadd'>
				<div class="any-flex">
					<div v-for='(item,index) in fwzttp3' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
					
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess4'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item> -->

			<el-form-item label="服务时长(min)">
				<el-input class='sty' type='number' v-model="formLabelAlign.goods_duration"></el-input>
			</el-form-item>
			<el-form-item label="服务流程">
				<el-input class='sty' v-model="formLabelAlign.goods_process"></el-input>
			</el-form-item>
			<el-form-item label="服务次数">
				<el-input class='sty' type='number' v-model="formLabelAlign.goods_frequency"></el-input>
			</el-form-item>
			<el-form-item label="有效期(天)">
				<el-input class='sty' type='number' v-model="formLabelAlign.goods_overdue_time"></el-input>
			</el-form-item>
			<el-form-item label="预约提醒">
				<el-input class='sty' v-model="formLabelAlign.goods_appointment"></el-input>
			</el-form-item>
			<el-form-item label="使用规则">
				<el-input class='sty' v-model="formLabelAlign.goods_use_rule"></el-input>
			</el-form-item>


			<el-form-item label="图文详情" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="upload" :data='e' list-type="picture-card" :limit='4' :file-list="fileList1"
				 :on-preview="handlePictureCardPreview" :auto-upload='false' :on-success='twxqcg' :multiple='true'>
					<i class="el-icon-plus"></i>
				</el-upload>
			</el-form-item>

			<!--图文详情修改页面 -->
			<el-form-item label="图文详情轮播" v-if='!isadd'>
				<div class="any-flex">
					<div v-for='(item,index) in twxqtp' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess1'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>


			<el-button v-if='isadd' type="success" @click="submitUpload">添加服务</el-button>
			<el-button v-else type="success" @click="exchgoods">修改服务</el-button>

		</el-form>


	</div>
</template>

<script>
	export default {
		data() {
			return {
				imgurls: this.global.imgurl,
				//服务轮播图
				fwlbt: [],
				twxqtp: [],
				fwzttp1: [],
				fwzttp2: [],
				fwzttp3: [],
				goods_id: '',
				statu: '',
				b: {
					url_name: 'fwzt1'
				},
				a: {
					url_name: 'fwlb'
				},
				c: {
					url_name: 'fwzt2'
				},
				d: {
					url_name: 'fwzt3'
				},
				e: {
					url_name: 'twxq'
				},
				fileList1: [],
				fileList: [],
				dialogImageUrl: '',
				dialogImageUrl1: '',
				dialogVisible: false,
				options: [{
					value: '选项1',
					label: '黄金糕'
				}],
				value: '',
				asss: 1,
				aaa: 1,
				//对齐方式
				labelPosition: 'left',
				formLabelAlign: {
					t_id: '',
					shop_id: this.global.shop_id(),
					goods_name: '',
					price: '',
					goods_effect: '',
					goods_duration: '',
					goods_process: '',
					goods_frequency: '',
					goods_overdue_time: '',
					goods_appointment: '',
					goods_use_rule: '',
					goods_planting_picture: '',
					picture: '',
					picture2: '',
					picture3: '',
					goods_picture_detail: '',
				},
			};
		},
		methods: {
			submitUpload() {
				this.$loading({
					text: '图片上传中'
				})
				// this.submitUploada()

				this.$refs.uploada.submit();
				this.$refs.uploadb.submit();
				// this.$refs.uploadc.submit();
				// this.$refs.uploadd.submit();

				this.$refs.upload.submit();
			},
			//修改主图1			
			fellsuccess2(response, file, fileList) {
				this.fileList = []
				let a = this.fwzttp1
				a[response.url_name] = response.path + ''

				setTimeout(() => {
					this.fwzttp1 = a
				}, 3000)

			},
			//修改主图2			
			// fellsuccess3(response, file, fileList) {
			// 	this.fileList = []
			// 	let a = this.fwzttp2
			// 	a[response.url_name] = response.path + ''
			// 	this.fwzttp2 = a
			// },
			//修改主图3		
			// fellsuccess4(response, file, fileList) {
			// 	this.fileList = []
			// 	let a = this.fwzttp3
			// 	a[response.url_name] = response.path + ''
			// 	this.fwzttp3 = a
			// },


			//修改图文详情的函数
			fellsuccess1(response, file, fileList) {
				this.fileList = []
				let a = this.twxqtp
				a[response.url_name] = response.path + ''
				
				setTimeout(() => {
					this.twxqtp = a
				}, 3000)
				
				
			},
			//修改轮播成功的函数
			fellsuccess(response, file, fileList) {
				this.fileList = []
				let a = this.fwlbt
				a[response.url_name] = response.path + ''
				setTimeout(() => {
					this.fwlbt = a
				}, 3000)
				
			},
			// 图文详情预览图
			handlePictureCardPreview(file) {
				this.dialogImageUrl1 = file.url;
				this.dialogVisible = true;
			},

			// 选择服务分类
			changsome(e) {
				this.formLabelAlign.t_id = e
			},
			//服务轮播成功
			fwlbcg(response, file, fileList) {
				this.formLabelAlign.goods_planting_picture = this.formLabelAlign.goods_planting_picture + response.path + ','
			},
			//商品主图1成功
			fwzt1cg(response, file, fileList) {
				this.formLabelAlign.picture = '' + response.path
				this.formLabelAlign.picture2 = '' + response.path
				this.formLabelAlign.picture3 = '' + response.path

				setTimeout(() => {
					this.$loading().close()
					this.addgoods()
				}, 5000)
			},
			//商品主图2成功
			// fwzt2cg(response, file, fileList) {
			// 	this.formLabelAlign.picture2 = '' + response.path
			// },
			//商品主图3成功
			// fwzt3cg(response, file, fileList) {
			// this.formLabelAlign.picture3 = '' + response.path


			// 
			// },
			// 图文详情上传成功
			twxqcg(response, file, fileList) {
				this.formLabelAlign.goods_picture_detail = this.formLabelAlign.goods_picture_detail + response.path + ','
			},
			// 服务轮播预览图
			handlePictureCardPreview1(file) {
				this.dialogImageUrl1 = file.url;
				this.dialogVisible = true;
			},
			//请求分类
			init() {
				this.utils.post({
					url: 'goods_type',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					let ss = []
					res.forEach((item, index) => {
						ss.push({
							value: item.t_id,
							label: item.t_name
						})
					})
					this.options = ss
				})
			},
			// 修改需要请求的接口
			inits() {
				this.utils.post({
					url: 'admin_goodsInfo',
					data: {
						goods_id: this.goods_id
					}
				}).then(objs => {
					let a = objs[0]
					this.formLabelAlign.goods_appointment = a.goods_appointment
					this.formLabelAlign.goods_duration = a.goods_duration
					this.formLabelAlign.goods_effect = a.goods_effect
					this.formLabelAlign.goods_frequency = a.goods_frequency
					this.formLabelAlign.goods_name = a.goods_name
					this.formLabelAlign.goods_overdue_time = a.goods_overdue_time
					this.formLabelAlign.goods_process = a.goods_process
					this.formLabelAlign.goods_use_rule = a.goods_use_rule
					this.formLabelAlign.price = a.price
					this.formLabelAlign.t_id = a.t_id
					let aa = a.goods_planting_picture.split(",")
					aa.pop()
					this.fwlbt = aa
					let bb = a.goods_picture_detail.split(",")
					bb.pop()
					this.twxqtp = bb
					this.fwzttp1.push(a.picture)
					// this.fwzttp2.push(a.picture)
					// this.fwzttp3.push(a.picture)
				})
			},
			// 保存数据的接口
			addgoods() {
				this.utils.post({
					url: 'goodsAdd',
					data: this.formLabelAlign
				}).then(res => {
					this.$router.push('/hellow/goods')
				})
			},
			exchgoods() {
				let a = this.fwlbt.join(",")
				let b = this.twxqtp.join(",")
				let c = this.fwzttp1.join(",")
				let d = this.fwzttp1.join(",")
				let e = this.fwzttp1.join(",")
				this.formLabelAlign.goods_picture_detail = b + ','
				this.formLabelAlign.goods_planting_picture = a + ','
				this.formLabelAlign.picture = c
				this.formLabelAlign.picture2 = d
				this.formLabelAlign.picture3 = e
				this.formLabelAlign.goods_id = this.goods_id
				this.utils.post({
					url: 'admin_goodsUpdate',
					data: this.formLabelAlign
				}).then(pop => {
					this.$router.push('/hellow/goods')
				})
			}
		},
		created() {
			let e = this.$route.query.addgoods
			let goods_id = this.$route.query.goods_id
			if (e == 789) {
				this.isadd = true
			} else if (e == 456) {
				this.isadd = false,
					this.goods_id = goods_id
			}
		},
		mounted() {
			if (this.isadd) {
				this.init()
			} else {
				this.inits()
			}
		}
	}
</script>

<style lang='scss'>
	.uplead {
		width: 120px;
		height: 100px;
		text-align: center;
		line-height: 100px;
		position: relative;
		border: 1px dashed #c7ddfa;

		.input {
			opacity: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.uplodes {
		position: relative;
	}

	.mysome {
		position: absolute;
		bottom: -40px;
		left: -13px;
	}

	.sty {
		width: 50%;
	}
</style>
