<template>
	<div class="management">
		<el-tabs v-model="activeName" @tab-click="handleClick">
			<el-tab-pane label="申请列表" name="first">
				<el-table :data="tableData" border style="width: 100%">
				
					<el-table-column prop="shop_reseller_time" label="申请日期" width="180">
					</el-table-column>
				
					<el-table-column prop="shop_id" label="申请店铺ID" width="180">
					</el-table-column>
				
					<el-table-column prop="shop_name" label="申请店铺">
					</el-table-column>
				
					<el-table-column prop="" label="">
				
						<template slot-scope="scope">
				
							<el-button type="primary" @click='addshop(scope.row,scope.$index)'>通过审核</el-button>
							<!-- <el-button type="success">忽略并删除此申请</el-button> -->
				
						</template>
					</el-table-column>
				
				</el-table>
			</el-tab-pane>
			<el-tab-pane label="分销店铺列表" name="second">
				<el-table :data="tableData1" border style="width: 100%">
				
					<el-table-column prop="shop_id" label="店铺ID" width="180">
					</el-table-column>
				
					<el-table-column prop="shop_name" label="店铺名称">
					</el-table-column>
					
					<el-table-column prop="admin_tel" label="联系方式">
					</el-table-column>
					
					<el-table-column prop="admin_tel" label="营业额">
					</el-table-column>
				
					<el-table-column prop="" label="操作">
				
						<template slot-scope="scope">
	<!-- 			
							<el-button type="primary" @click='addshop(scope.row)'>通过审核</el-button>
	-->						
							<el-button type="danger" @click='delshop(scope.row,scope.$index)'>取消分销权利</el-button> 
				
						</template>
					</el-table-column>
				
				</el-table>
			</el-tab-pane>

		</el-tabs>



	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [],
				tableData1: [],
				activeName: 'first'
			}
		},
		methods: {
			//取消分销权利
			delshop(e,a){
				this.$confirm('此操作将永久取消该店铺分销权, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
				         
						 this.utils.post({
							 url:'admin_reseller_delete',
							 data:{
								 shop_id:e.shop_id
							 }
						 }).then(res=>{
							 this.tableData1.splice(a,1)
						 })
						 
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消'
				          });          
				        });
			},
			
			// 点击切换头部
			handleClick(tab, event) {
				console.log(tab, event);
			},
			// 通过
			addshop(e,a) {
				this.utils.post({
					url: 'admin_reseller_examine',
					data: {
						shop_id: e.shop_id
					}
				}).then(res=>{
					this.tableData.splice(a,1)
				})

			},
			// admin_reseller_list
			inits() {
				this.utils.post({
					url: 'admin_reseller_list'
				}).then((res) => {
					this.global.a({a:'分销店铺列表',res:res})
					this.tableData1 = res.data
				})
			},
			init() {
				this.utils.post({
					url: 'admin_apply_reseller_list'
				}).then((res) => {
					this.global.a(res)
					
					res.data.forEach((item,index)=>{
						item.shop_reseller_time = this.global.timelaver( item.shop_reseller_time * 1000)
					})
					this.tableData = res.data
				})
			}
		},
		mounted() {
			this.init()
			this.inits()
		}
	}
</script>

