<template>
	<div class="booking">

		<div class="any-flex">
			<el-button type="primary">限时抢列表</el-button>
		</div>

 
		<el-table :data="tableData" style="width: 100%">
			<el-table-column v-if='this.global.is_admin()' prop="shop_name" label="限时抢店铺" width="180">
			</el-table-column>
			<el-table-column prop="goods_name" label="限时抢商品" width="180">
			</el-table-column>
			<el-table-column prop="limited_price" label="限时抢价格">
			</el-table-column>

			<el-table-column prop="limited_prople" label="限时抢人数">
			</el-table-column>

			<el-table-column prop="limited_ready_prople" label="已抢">
			</el-table-column>

			<el-table-column prop="limited_stop_time" label="到期时间">

			</el-table-column>

			<el-table-column prop="address" label="操作">
				<template slot-scope="scope">
					<el-button type="danger" @click='deletes(scope.row , scope.$index)'>取消限时抢</el-button>
				</template>
			</el-table-column>

		</el-table>
		
		
		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [],
				currentPage: 0,
				total: 0,
				pagesize: 0,
			};
		},
		methods: {
			deletes(e, a) {
				this.$confirm('此操作将永久移除限时抢, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.utils.post({
						url: 'admin_Limited',
						data: {
							goods_id: e.goods_id,
							limited_buy: 0
						}
					}).then(res => {
						this.tableData.splice(a, 1)
					})

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			init() {
				this.utils.post({
					url: 'admin_Limited_list',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					res.data.forEach((item, index) => {
						item.limited_stop_time = this.global.timelaver(item.limited_stop_time * 1000)
					})
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					this.tableData = res.data
				})
			},
			changepages(e) {
				this.utils.post({
					url: 'admin_Limited_list',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					this.tableData = res.data
				})
			
			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style>
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
</style>
