<template>
	<div class='order'>
		<el-table :data="tableData" style="width: 100%">
			
			<el-table-column prop="order_id" label="订单ID" width="150" v-if='this.global.is_shop()'>
			</el-table-column>
			
			<el-table-column prop="create_time" label="购买日期" width="180">
			</el-table-column>
			
			<el-table-column prop="order_no" label="订单编号" width="180">
			</el-table-column>

			<el-table-column prop="order_status" label="订单状态" width="180">
				<template slot-scope="scope">
					<div v-if="scope.row.order_status == 0"> 未支付</div>
					<div v-if="scope.row.order_status == 1"> 已支付</div>
				</template>
				
			</el-table-column>

			<el-table-column prop="price" label="购买金额" width="180" v-if='this.global.is_shop()'>
			</el-table-column>
			
			<el-table-column prop="total_price" label="订单金额" width="180" v-if='this.global.is_admin()'>
			</el-table-column>
			
			<el-table-column prop="wx_name" label="购买用户" width="180" v-if='this.global.is_admin()'>
			</el-table-column>
			
			<el-table-column prop="" label="操作" width="180" v-if='this.global.is_admin()'>
				<template slot-scope="scope">
					<el-button type="primary" @click='gomes(scope.row)'>查看详情</el-button>
				</template>
			</el-table-column>
			
		</el-table>



		<el-dialog title="收货地址" :visible.sync="dialogTableVisible" width='90%'>
			<el-table :data="gridData">
				<el-table-column property="order_id" label="订单ID" width="80"></el-table-column>
				<el-table-column property="order_no" label="订单编号" width="200"></el-table-column>
				<el-table-column property="order_status" label="订单状态" width="150">
					<template slot-scope="scope">
						<div v-if="scope.row.order_status == 0"> 未支付</div>
						<div v-if="scope.row.order_status == 1"> 已支付</div>
						<div v-if="scope.row.order_status == 2"> 待发货</div>
						<div v-if="scope.row.order_status == 3"> 已发货</div>
						<div v-if="scope.row.order_status == 4"> 确认收货</div>
						<div v-if="scope.row.order_status == 5"> 已完成</div>
				
					</template>
				</el-table-column>
				<el-table-column property="create_time" label="购买日期" width="190">
				</el-table-column>
				<el-table-column property="shop_name" label="店铺名称" width="250"></el-table-column>
				<el-table-column property="shop_id" label="店铺ID" width="150"></el-table-column>
				<el-table-column property="goods_id" label="商品ID" width="150"></el-table-column>
				<el-table-column property="goods_name" label="商品名称" width="200"></el-table-column>
				<el-table-column property="buy_num" label="购买数量" width="150"></el-table-column>
				<el-table-column property="price" label="购买价格" width="150"></el-table-column>

			</el-table>
		</el-dialog>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [],
				dialogTableVisible: false,
				gridData: [],
			}
		},
		methods: {

			gomes(e) {
				this.gridData = []
				this.dialogTableVisible = true
				this.utils.post({
					url: 'admin_orderDetail',
					data: {
						order_id: e.order_id
					}
				}).then(res => {
					res.forEach((item, index) => {
						item.create_time = this.global.timelaver(item.create_time * 1000)
					})
					this.gridData = res
				})
			},
			init() {
					this.utils.post({
						url: 'admin_orderList',
						data:{ shop_id:this.global.shop_id() }
					}).then(res => {
						this.global.a(res)
						res.forEach((item, index) => {
							item.create_time = this.global.timelaver(item.create_time * 1000)
						})
						this.tableData = res
					})			
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

