<template>
	<div class="myshop">
		<div class="anybody any-flex">
			<div class="shop">
				<div class="shop_name"><span style="margin-left:9px;"></span> {{shop_list.shop_name}}  <span class="expas"> <el-link type="primary" @click='exmima'>修改密码</el-link></span> </div>
				
				<div class="shop_image">
					<span><span style="color: red;">*</span> 店铺logo: </span>
					<div class="any-flex">
						<div v-for='(item,index) in logol' style="margin: 10px;">
							<el-image style="width: 50px; height: 50px" :src="imgurls + item" :preview-src-list='[ imgurls + item]' fit="fill"></el-image>
							<!-- 图片上传 -->
							<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess1'
							 :limit="1" :file-list='fileList'>
								<el-button size="small" type="primary">点击修改</el-button>
							</el-upload>
						</div>
					</div>
				</div>
				
				<div class="shop_image">
					<span><span style="color: red;">*</span> 店铺主图 : </span>
					<div class="any-flex">
						<div v-for='(item,index) in shopimg' style="margin: 10px;">
							<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[ imgurls + item]' fit="fill"></el-image>
							<!-- 图片上传 -->
							<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess'
							 :limit="1" :file-list='fileList'>
								<el-button size="small" type="primary">点击修改</el-button>
							</el-upload>
						</div>
					</div>
				</div>

				<div class="deleces"> <span style="color: red;">*</span> <span class="spans"> 特色项目 : </span>
					<el-tag :key="tag" v-for="tag in dynamicTags" :closable='exchis' :disable-transitions="false" @close="handleClose(tag)">
						{{tag}}
					</el-tag>
					<div v-if='exchis'>
						<el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small"
						 @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
						</el-input>
						<el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
					</div>
				</div>

				<div class="any-flex" style="padding: 10px 0;">
					<div class=""><span style="color: red;">*</span> 店铺简介 : </div>
					<el-input style='width: 50%;margin-left: 10px;' :disabled="!exchis" v-model="shop_list.shop_desc" placeholder="请用一句话介绍您的店铺"></el-input>
				</div>
				<div class="any-flex" style="padding: 10px 0;">
					<div class="shop_name"><span style="color: red;">*</span> 营业时间 :</div>
					<el-input style='width: 50%;margin-left: 10px;' :disabled="!exchis" v-model="shop_list.shop_bus" placeholder=" 如:周一至周五 8:00 - 20:00 "></el-input>
				</div>

				<div class="shop_name" style="margin: 10px 0;"><span style="color: red;">*</span> 特色服务 :
					<el-tag :key="tag" v-for="tag in dynamicTags1" :closable='exchis' :disable-transitions="false" @close="handleClose1(tag)">
						{{tag}}
					</el-tag>
					<div v-if='exchis'>
						<el-input class="input-new-tag" v-if="inputVisible1" v-model="inputValue1" ref="saveTagInput1" size="small"
						 @keyup.enter.native="handleInputConfirm1" @blur="handleInputConfirm1">
						</el-input>
						<el-button v-else class="button-new-tag" size="small" @click="showInput1">+ New Tag</el-button>
					</div>
				</div>
				<div class="shop_name" style="padding: 10px 0;"> <span style="margin-left:9px;"></span>店铺地址 : {{shop_list.shop_address_provice + shop_list.shop_address_city + shop_list.shop_address_area }} </div>
				<div class="iphone shop_name" style="padding: 10px 0;"> <span style="margin-left:9px;"></span> 联系电话 : {{shop_list.admin_tel}}</div>


				<el-button v-if='!exchis' type="primary" @click='gosomecha'>修改店铺信息</el-button>

				<div v-else>
					<el-button type="primary" @click = exmess>确认</el-button>
					<el-button type="success" @click='aaaa'>取消</el-button>
				</div>

			</div>

			<div style="width: 50%;height: 100%;">
				
				<!-- <div style="margin:10px 0;">总营业额: {{dd}}</div> -->
				
				<div>本周营业额</div>
				
				<div id='contents' class="e-charse"></div>

			</div>

		</div>
	</div>
</template>

<script>
	// 引入 ECharts 主模块
	var echarts = require('echarts/lib/echarts');
	// 引入柱状图
	require('echarts/lib/chart/bar');
	// 引入提示框和标题组件
	require('echarts/lib/component/tooltip');
	require('echarts/lib/component/title');
	export default {
		data() {
			return {
				///-----店铺修改中间键
				exchis: false,
				logol:[],
				shop_list:{},
				//----------------
				shopimg: ['/imgadvertis/9.jpg'],
				fileList: [],
				imgurls: this.global.imgurl,
				// tag小标签
				dynamicTags: ['标签一', '标签二', '标签三'],
				inputVisible: false,
				inputValue: '',
				//-------------------
				dynamicTags1: ['789', '654', '321'],
				inputVisible1: false,
				inputValue1: '',
				admin_id:'',
				cc:'123',
				dd:''
			}
		},
		methods: {
			//点击确认
			exmess(){
				this.shop_list.shop_id = this.global.shop_id()
				this.shop_list.shop_img = this.shopimg.join()
				this.shop_list.shop_logo = this.logol.join()
				this.shop_list.shop_project = this.dynamicTags.join(',')
				this.shop_list.shop_service = this.dynamicTags1.join(',')
				this.exchis = false
				
				this.utils.post({
					url:'admin_shop_update',
					data:this.shop_list
				}).then(res=>{
					this.global.a('修改成功')
				})
			},
			exmima(){
				this.global.a(this.admin_id)
				this.$router.push({
					path:'/expassword',
					query:{
						admin_id: this.admin_id
					}
				})
			},
			//点击取消
			aaaa(){
				this.$router.go(0)
			},
			fellsuccess(response, file, fileList) {
				this.fileList = []
				let a = this.shopimg
				a[response.url_name] = response.path + ''
				
				setTimeout( ()=>{
				this.shopimg = a
				this.exmess()
				},3000)

			},
			// logol
			fellsuccess1(response, file, fileList) {
				this.fileList = []
				
				let a = []
				
				this.global.a(a)
				
				a[response.url_name] = response.path + ''
				
				setTimeout( ()=>{
					this.logol = a
					this.exmess()
				},3000)
				
				
				
				
				
			},
			//特色项目 小标签方法
			handleClose(tag) {
				this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
			},

			showInput() {
				this.inputVisible = true;
				this.$nextTick(_ => {
					this.$refs.saveTagInput.$refs.input.focus();
				});
			},

			handleInputConfirm() {
				let inputValue = this.inputValue;
				if (inputValue) {
					this.dynamicTags.push(inputValue);
				}
				this.inputVisible = false;
				this.inputValue = '';
			},
			/////-----------------
			//特色服务 小标签方法
			handleClose1(tag) {
				this.dynamicTags1.splice(this.dynamicTags1.indexOf(tag), 1);
			},

			showInput1() {
				this.inputVisible1 = true;
				this.$nextTick(_ => {
					this.$refs.saveTagInput1.$refs.input.focus();
				});
			},

			handleInputConfirm1() {
				let inputValue1 = this.inputValue1;
				if (inputValue1) {
					this.dynamicTags1.push(inputValue1);
				}
				this.inputVisible1 = false;
				this.inputValue1 = '';
			},
			gosomecha() {
				this.exchis = true
			},
			init() {
				this.utils.post({
					url: 'admin_shop',
					data: {
						shop_id: this.global.shop_id()
					}

				}).then(res => {
					
					
					let ss = res.shopInfo
					
					let cc = []
					
					cc[0] = res.sixdays_total_num
					cc[1] = res.fivedays_total_num
					cc[2] = res.fourdays_total_num
					cc[3] = res.threedays_total_num
					cc[4] = res.before_yesterdays_total_num
					cc[5] = res.yesterdays_total_num
					cc[6] = res.todays_total_num
					
					this.shop_list = ss
					
					this.shopimg = [ ss.shop_img+'' ]
					
					this.logol = [ss.shop_logo + '' ] 
					
					this.admin_id = ss.admin_id
					
					this.dynamicTags = ss.shop_project.split(",")
					
					this.dynamicTags1 = ss.shop_service.split(',')
						
					
					// 基于准备好的dom，初始化echarts实例
					var myChart = echarts.init(document.getElementById('contents'));
					
					// 绘制图表
					myChart.setOption({
						color: ['#3398DB'],
						tooltip: {
							trigger: 'axis',
							axisPointer: { // 坐标轴指示器，坐标轴触发有效
								type: 'line' // 默认为直线，可选为：'line' | 'shadow'
							}
						},
						grid: {
							left: '3%',
							right: '4%',
							bottom: '3%',
							containLabel: true
						},
						xAxis: [{
							type: 'category',
							data: ['六天前', '五天前', '四天前', '三天前', '二天前', '一天前', '今天'],
							axisTick: {
								alignWithLabel: true
							}
						}],
						yAxis: [{
							type: 'value'
						}],
						series: [{
							name: '购买',
							type: 'bar',
							barWidth: '60%',
							data: cc
						}]
					});
					
					
					
				})
			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.myshop {
		color: #666;
		// background: #ccc;
	}
	.expas{
		margin-left: 50px;
	}

	.spans {
		margin-right: 10px;
	}

	.anybody {

		justify-content: flex-start;

		.e-charse {
			width: 100%;
			height: 500px;
			background: #fff;
		}
	}

	.shop {
		width: 50%;

		.shop_name {
			margin: 10px 0 10px 2px;
			// margin-left:2px ;
		}
	}

	.el-tag+.el-tag {
		margin-left: 10px;
	}

	.button-new-tag {
		margin-left: 10px;
		height: 32px;
		line-height: 30px;
		padding-top: 0;
		padding-bottom: 0;
	}

	.input-new-tag {
		width: 90px;
		margin-left: 10px;
		vertical-align: bottom;
	}
</style>
