<template>
	<div class="coupon">

		<!-- 搜索 -->
		<div style="margin-top: 15px;" v-if="this.global.is_admin()">
			<el-input placeholder="请输入内容" v-model="input3" class="input-with-select">
				<el-select v-model="select" slot="prepend" placeholder="请选择">
					<el-option label="店铺名" value="1"></el-option>
					<el-option label="商品名" value="2"></el-option>
				</el-select>
				<el-button slot="append" @click='getone' icon="el-icon-search"></el-button>
			</el-input>
		</div>


		<div class="any-flex">

			<router-link class="router-color" to="/hellow/coupon">
				<el-button type="primary">优惠券列表</el-button>
			</router-link>

			<router-link class="router-color" to="/hellow/addcoupon" v-if='this.global.is_shop()'>
				<el-button type="success">添加优惠券</el-button>
			</router-link>
		</div>


		<el-table :data="tableData" border style="width: 90%">

			<el-table-column prop="goods_id" label="商品ID" width="100">
			</el-table-column>

			<el-table-column prop="coupon_names" label="优惠券名称" width="180">
			</el-table-column>

			<el-table-column prop="coupon_num" label="优惠券数量" width="100">
			</el-table-column>

			<el-table-column prop="coupon_inser" label="已领取优惠券数量" width="180">
			</el-table-column>

			<el-table-column prop="" label="优惠券类型" width="180">
				<template slot-scope="scope">
					<div> {{scope.row.coupon_type == 0 ? '满减' : '折扣'}}</div>
				</template>

			</el-table-column>

			<el-table-column prop="" label="添加时间">

				<template slot-scope="scope">
					<div> {{ scope.row.coupon_start_time | some  }}</div>
				</template>

			</el-table-column>

			<el-table-column prop="shop_name" label="优惠店铺" v-if='this.global.is_admin()'>
			</el-table-column>

			<el-table-column prop="goods_name" label="优惠商品">
			</el-table-column>

		</el-table>

		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>


	</div>
</template>

<script>
	export default {
		name: 'coupon',
		data() {
			return {
				input3: '',
				select: '',
				total: 10,
				currentPage: 1,
				pagesize: 4,
				tableData: []
			}
		},
		methods: {
			getone(e) {

				if(this.select == 1){
					let shop_name = this.input3
					let goods_name = ''
					this.selects(shop_name,goods_name)
				}else if(this.select == 2){
					let shop_name = ''
					let goods_name = this.input3
					this.selects(shop_name,goods_name)
				}

			},
			selects(e,a){
				this.utils.post({
					url:'couponsearch',
					data:{
						goods_name:a,
						shop_name:e
					}
				}).then(res=>{
					this.global.a(res)
					this.tableData = res.data
				})
			},
			// 获取优惠券
			init() {
				this.utils.post({
					url: 'couponexhibition',
					data:{
						shop_id:this.global.shop_id()
					}
				}).then(res => {
					this.tableData = res.data
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
				})
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'couponexhibition',
					data: {
						page: e
					}
				}).then(res => {
					this.tableData = res.data
				})
			}
		},
		filters: {

			some: time => {
				var date = new Date(time * 1000);
				let Y = date.getFullYear() + '-';
				let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
				let D = date.getDate() + ' ';
				let h = date.getHours() + ':';
				let m = date.getMinutes() + ':';
				let s = date.getSeconds();
				var str = (Y + M + D + h + m + s)
				return str;
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.any-flex {
		justify-content: flex-start;
		margin: 20px 0;
	}

	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
</style>
