<template>
	<div class="nopassword">
		<div class='heanders any-flex'>
			<div class="logo">美丽共享联盟</div>
			<div class="con"></div>
			<div class="wangj">忘记密码 ？</div>
		</div>
		<div class="bztiao">
			<el-steps :active="active" finish-status="success">
				<el-step title="验证身份"></el-step>
				<el-step title="重置密码"></el-step>
				<el-step title="修改成功"></el-step>
			</el-steps>
		</div>

		<div class="truecon">
			<!-- //第一步 -->
			<div style='width:70%; margin:0 auto;' v-if='active == 1'>
				<div class="any-flex tosome">
					<div> 请输入手机号 : </div>
					<el-input v-model="inputa" class='inputa' placeholder="请输入手机号"></el-input>
					<el-button style='margin-left: 10px;' @click='timeout' v-if="text == '获取验证码' ">{{text}}</el-button>
					<el-button style='margin-left: 10px;' v-else>{{text}}</el-button>
				</div>

				<div class="any-flex tosome">
					<div> 请输入验证码 : </div>
					<el-input v-model="inputb" class='inputa' placeholder="请输入验证码"></el-input>
				</div>

				<div class='any-flex' style='align-items: center;'>
					<el-button type="success" style="width: 150px; margin: 30px auto;" @click="next(1)">下一步</el-button>
				</div>
			</div>

			<!-- //第二步 -->
			<div style='width:60%; margin:0 auto;' v-if='active == 2'>
				<div class="any-flex tosome">
					<div> 请输入新密码 : </div>
					<el-input v-model="input1" type='password' class='inputa' placeholder="请输入新密码"></el-input>
				</div>

				<div class="any-flex tosome">
					<div> 请再次输入密码 : </div>
					<el-input v-model="input2" type='password' class='inputa' placeholder="请再次输入密码"></el-input>
				</div>

				<el-button type="success" style="width: 150px; margin: 30px auto;" @click="next(2)">下一步</el-button>
			</div>

			<div class='any-flex' style='align-items: center;'>
				<div style='width:20%; margin:50px auto;' v-if='active == 3'>
					<div class="wangj">密码已重置成功！</div>
					<el-button type="success" style="width: 150px; margin: 30px auto;" @click="next(3)">去登陆</el-button>
				</div>
			</div>

		</div>



	</div>
</template>

<script>
	export default {
		data() {
			return {
				active: 1,
				inputb: '',
				inputa: '',
				input1: '',
				input2: '',
				text: '获取验证码'
			}
		},
		methods: {
			// 获取验证码
			timeout() {


				axios.post('admin_forgetPwd', {
						phone_num: this.inputa
					})
					.then(res => {

						if (res.data.code == 1) {
							this.$message.error(res.data.msg);
						} else if (res.data.Code == 'OK') {

							this.text = '60s'
							let i = 60
							var intav = setInterval(() => {
								i--
								this.text = i + 's'
								if (i <= 0) {
									this.text = '获取验证码'
									// clearSetin
									window.clearInterval(intav);
								}
							}, 1000)
						}
					})

			},
			next(e) {
				// if (this.active++ > 2) this.active = 0;
				if (e == 1) {

					axios.post('admin_message_check', {
						phone_num: this.inputa,
						code: this.inputb
					}).then(res => {
						if (res.data.code == 0) {
							this.global.a(res)
							this.active++
						} else if (res.data.code == 1) {
							this.$message.error(res.data.msg);
						}else if (res.data.code == 2) {
								this.$message.error(res.data.msg);
						}

					})
				}
				if (e == 2) {
					if(this.input2 == this.input1){
						axios.post('admin_passwordUpdate', {
							phone_num: this.inputa,
							update_admin_pwd:this.input2,
						}).then(res => {
							if (res.data.code == 0) {
								this.global.a(res) 
								this.active++
							} else if (res.data.code == 1) {
								this.$message.error(res.data.msg);
							}else if (res.data.code == 2) {
								this.$message.error(res.data.msg);
							}
						})
					}else{
						this.$message.error('两次输入密码不一致');
					}
				}
				if(e == 3){
					this.$router.push('/')
				}
			}
		}
	}
</script>
<style lang="scss">
	.truecon {
		margin: 0 auto;
		width: 40%;
		height: 400px;
		// background: #ed4444;
		overflow: hidden;

		.tosome {
			font-size: 10px;
			margin-top: 40px;

			// justify-content: space-between;
			.inputa {
				width: 60%;
				margin-left: 10px;
			}

		}
	}

	.bztiao {
		width: 40%;
		margin: 40px auto;
	}

	.heanders {
		width: 100%;
		height: 100px;

		background: #ed4444;

		.logo {
			width: 20%;
			text-align: center;
			line-height: 100px;
			font-size: 40px;
			font-weight: 600;
			color: #fff;
		}

		.con {
			width: 2px;
			background: #666;
			height: 90px;
		}

		.wangj {
			width: 20%;
			height: 100px;
			line-height: 100px;
			font-size: 20px;
			text-align: center;
			color: #fff;
		}
	}
</style>
