<template>
  <div class="login"> 
    <!-- 搜索 -->
    	<div style="margin-top: 15px;">
    		<el-input placeholder="请输入内容" v-model="input3" class="input-with-select">
    			<el-select v-model="select" slot="prepend" placeholder="请选择">
    				<el-option label="昵称" value="1"></el-option>
    				<el-option label="手机号" value="2"></el-option>
    			</el-select>
    			<el-button slot="append" @click='getone' icon="el-icon-search"></el-button>
    		</el-input>
    	</div>
   
    	<!-- 列表 -->
    	<el-table :data="tableData" style="width: 100%" >
    		<el-table-column fixed prop="shop_id" label="ID" width="150">
    		</el-table-column>
    		<el-table-column fixed prop="shop_name" label="店铺名" width="200">
    		</el-table-column>
			<el-table-column prop="shop_contacts" label="联系人" width="200">
			</el-table-column>
    		<el-table-column prop="shop_phone" label="联系方式(账号)" width="300">
    		</el-table-column>
    		<el-table-column prop="shop_sales" label="店铺总收入" width="120">
    		</el-table-column>
			<el-table-column prop="shop_account" label="余额" width="100">
			</el-table-column>

    		<el-table-column prop="" label="操作" width="400">
				<template slot-scope="scope">
					<el-button type="primary" @click='blockshop(scope.$index, scope.row)'>取消拉黑</el-button>
					<el-button type="danger" @click='businessdelete(scope.$index, scope.row)'>移除该用户</el-button>
				</template>
    		</el-table-column>
    
    	</el-table> 
    	
    	<!-- 分页 -->
		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" 
			@current-change='changepages'
			:page-count='currentPage'
			>
				
			</el-pagination>
		</div>
    </div>
  </div>
</template>
 
<script>
	export default {
		name: "login",
		data() {
			return {
				total:0,
				currentPage: 1,
				tableData: [], // 数据列表
				input3: '', //搜索框内容
				select: '' //搜索框的东西 1.昵称 2.手机号
			}
		},
		methods: {
			// 获取拉黑列
			init() {
				this.utils.post({
					url: 'businessblacktype'
				}).then(res => {
					this.tableData = res.data
					this.total = res.total
					this.pageSizes = res.pre_page
				})
			},
			// 选择查询
			getone() {
				if (this.select == '') {
					this.$message.error('请选择查询条件');
				} else if (this.select == 1) {
					let shop_phone = ''
					let shop_contacts = this.input3
					this.goon(shop_phone, shop_contacts)
				} else if (this.select == 2) {
					let shop_contacts = ''
					let shop_phone = this.input3
					this.goon(shop_phone, shop_contacts)
				}
			},

			// 查询
			goon(shop_phone, shop_contacts) {
				this.utils.post({
					url: 'serachbusiness',
					data: {
						shop_phone: shop_phone,
						shop_contacts: shop_contacts
					}
				}).then(res => {
					this.tableData = res
				})
			},
			// 取消拉黑
			blockshop(e,a){
				        this.$confirm('此操作将商户取消拉黑, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
				          this.$message({
				            type: 'success',
				            message: '移除成功!',
				          });
						  this.utils.post({
						  	url:'remove',
						  	data:{
						  		shop_id:a.shop_id,
						  	}
						  }).then(res=>{
						  	this.tableData.splice(e,1)
						  })
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消移除'
				          });          
				        });
				      

			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'businessblacktype',
					data:{
						page:e
					}
				}).then(res => {
					this.tableData = res.data
				})
			},
			// 移除商户
			businessdelete(a,e){
				this.$confirm('此操作将永久删除该商家, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
							
							this.utils.post({
								url:'businessdelete',
								data:{
									shop_id:e.shop_id
								}
							}).then(res=>{
									this.tableData.splice(a,1)
							})
				          this.$message({
				            type: 'success',
				            message: '删除成功!'
				          });
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消删除'
				          });          
				        });
				
				
			
				
			}
		},
		mounted() {
			this.init()
		}
	};
</script>


<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}

	.some-message {
		margin: 20px 0;
		width: 100%;
		height: 100px;
		background: #72edfb;
		display: flex;
		justify-content: space-between;

		.io {
			width: 20%;
			height: 100%;
			line-height: 50px;
			text-align: center;

			.hd {
				font-weight: 600;
			}
		}
	}



	.el-select .el-input {
		width: 130px;

	}

	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
</style>