<template>
	<div class="addmygoods">

		<div v-if='isadd'>新增商品</div>
		<div v-else>修改商品</div>
		<el-form :label-position="labelPosition" label-width="130px" :model="formLabelAlign">
			<el-form-item label="商品名称">
				<el-input v-model="formLabelAlign.re_goods_name" class='inputwidth'></el-input>
			</el-form-item>
			<el-form-item label="商品价格">
				<el-input type='number' v-model="formLabelAlign.re_goods_price" class='inputwidth'></el-input>
			</el-form-item>

			<el-form-item label="商品主图" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="uploadb" :data='{url_name: "fwzt1"}' list-type="picture-card"
				 :limit='1' :file-list="fileList1" :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwzt1cg'
				 :multiple='false'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item>

			<!-- 商品主图修改页面 -->
			<el-form-item label="商品主图" v-if='isexc'>
				<div class="any-flex">
					<div v-for='(item,index) in re_goods_picture' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]  ' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccessb'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>


			<el-form-item label="商品功效">
				<el-input class='inputwidth' v-model="formLabelAlign.re_goods_introduction"></el-input>
			</el-form-item>

			<el-form-item label="商品库存">
				<el-input class='inputwidth' type='number' v-model="formLabelAlign.re_goods_stock"></el-input>
			</el-form-item>

			<el-form-item label="是否开启分销">
				<el-switch v-model="formLabelAlign.is_distribution" @change='is_dis' active-color="#13ce66" inactive-color="#ff4949">
				</el-switch>
			</el-form-item>

			<!-- 轮播新增页面 -->
			<el-form-item label="商品轮播" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="uploada" :data='{url_name: "1"}' list-type="picture-card" :limit='4'
				 :file-list="fileList1" :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwlbcg' :multiple='true'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item>

			<!-- 轮播修改页面 -->
			<el-form-item label="商品轮播" v-if='isexc'>
				<div class="any-flex">
					<div v-for='(item,index) in re_goods_planting_picture' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]  ' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>


			<el-form-item label="图文详情" v-if='isadd'>
				<el-upload class='uplodes' action="upload" ref="uploadc" :data='{url_name: "1"}' list-type="picture-card" :limit='4'
				 :file-list="fileList1" :on-preview="handlePictureCardPreview1" :auto-upload='false' :on-success='fwlbcg1'
				 :multiple='true'>
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl1" alt="">
				</el-dialog>
			</el-form-item>

			<!-- 图文详情修改页面 -->
			<el-form-item label="图文详情" v-if='isexc'>
				<div class="any-flex">
					<div v-for='(item,index) in re_goods_picture_detail' style="margin: 10px;">
						<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
						<!-- 图片上传 -->
						<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccessa'
						 :limit="1" :file-list='fileList'>
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</div>
				</div>
			</el-form-item>


			<el-form-item label="生产时间">
				<el-date-picker v-model="value1" type="date" placeholder="选择日期">
				</el-date-picker>
			</el-form-item>

			<el-form-item label="过期时间">
				<el-date-picker v-model="value2" type="date" placeholder="选择日期">
				</el-date-picker>
			</el-form-item>

			<el-button v-if='isadd' type="success" @click='addmygoods'>添加商品</el-button>
			
			<el-button v-if='isexc' type="success" @click='exmygoods'>确认修改</el-button>

		</el-form>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				imgurls:this.global.imgurl,
				fileList:[],
				isadd: false,
				isexc: false,
				re_goods_id:'',
				re_goods_planting_picture:[],
				re_goods_picture_detail:[],
				re_goods_picture:[],
				fileList1: [],
				dialogImageUrl1: '',
				dialogVisible: false,
				// 开始时间
				value1: '',
				value2: '',
				//对齐方式
				labelPosition: 'left',
				formLabelAlign: {
					re_goods_name: '',
					re_goods_price: '',
					re_goods_picture: '', //商品主图
					re_goods_introduction: '',
					re_goods_stock: '',
					is_distribution: false,
					re_goods_planting_picture: '',
					re_goods_picture_detail: '',
					re_production_time: '',
					re_expiration_time: ''


				}
			}
		},
		methods: {
			is_dis(e) {
				this.is_distribution = e
			},
			addmygoods() {
				this.$loading({
					text: '图片上传中'
				})
				this.$refs.uploada.submit();
				this.$refs.uploadc.submit();
				this.$refs.uploadb.submit();
			},
			//商品轮播成功
			fwlbcg(response, file, fileList) {
				this.formLabelAlign.re_goods_planting_picture = this.formLabelAlign.re_goods_planting_picture + response.path + ','
				//2
			},
			//图文详情
			fwlbcg1(response, file, fileList) {
				this.formLabelAlign.re_goods_picture_detail = this.formLabelAlign.re_goods_picture_detail + response.path + ','
				//3
			},
			//商品主图成功
			fwzt1cg(response, file, fileList) {
				this.formLabelAlign.re_goods_picture = '' + response.path
				this.formLabelAlign.re_production_time = this.value1.getTime() / 1000
				this.formLabelAlign.re_expiration_time = this.value2.getTime() / 1000
				this.formLabelAlign.shop_id = this.global.shop_id()
				if (this.formLabelAlign.is_distribution) {
					this.formLabelAlign.is_distribution = 1
				} else {
					this.formLabelAlign.is_distribution = 0
				}
				// 4
				this.utils.post({
					url: 'admin_reseller_upload_goods',
					data: this.formLabelAlign
				}).then(res => {
					this.$loading().close()
					this.$router.push('/mygoods')
				})

			},
			// 商品主图
			handlePictureCardPreview1(file) {
				this.dialogImageUrl1 = file.url;
				this.dialogVisible = true;
			},
			
			
			
			//修改轮播成功
			fellsuccess(response, file, fileList) {
				this.fileList = []
				let a = this.re_goods_planting_picture
				a[response.url_name] = response.path + ''
				
				setTimeout(() => {
					this.re_goods_planting_picture = a
				}, 4000)
				
			},
			// 修改图文详情成功
			fellsuccessa(response, file, fileList){
				this.fileList = []
				let a = this.re_goods_picture_detail
				a[response.url_name] = response.path + ''
				
				setTimeout(() => {
					this.re_goods_picture_detail = a
				}, 4000)
				
			},
			// 修改商品主图成功
			fellsuccessb(response, file, fileList){
				this.fileList = []
				let a = this.re_goods_picture
				a[response.url_name] = response.path + ''
				
				setTimeout(() => {
					this.re_goods_picture = a
				}, 3000)
				
			},
			exmygoods(){
				this.formLabelAlign.shop_id = this.global.shop_id()
				this.formLabelAlign.re_goods_id = this.re_goods_id
				this.formLabelAlign.re_goods_picture = this.re_goods_picture.join()
				let a = this.re_goods_planting_picture.join()
				let b = this.re_goods_picture_detail.join()
				this.formLabelAlign.re_goods_planting_picture = a + ','
				this.formLabelAlign.re_goods_picture_detail = b + ','
				this.formLabelAlign.is_distribution = this.formLabelAlign.is_distribution ? 1 : 0 ; 
				this.formLabelAlign.re_production_time = new Date(this.value1).getTime() / 1000
				this.formLabelAlign.re_expiration_time = new Date(this.value2).getTime() / 1000
				
				this.global.a(this.formLabelAlign)
				this.utils.post({
					url:'admin_reseller_goods_update',
					data:this.formLabelAlign
				}).then(res=>{
					this.$router.push('/mygoods')
				})
			}

		},
		created() {
			this.global.a(this.$route.query)
			if (this.$route.query.run == 'add') {
				this.isadd = this.$route.query.addmygoods
			}
			if (this.$route.query.run == 'exc') {
				this.global.a(this.$route.query.goodsmsg)
				let e = this.$route.query.goodsmsg
				this.re_goods_id = e.re_goods_id
				this.formLabelAlign.re_goods_name = e.re_goods_name
				this.formLabelAlign.re_goods_price = e.re_goods_price
				this.formLabelAlign.re_goods_introduction = e.re_goods_introduction
				this.formLabelAlign.re_goods_stock = e.re_goods_stock
				this.formLabelAlign.is_distribution = e.is_distribution == 0 ? false : true;
				this.value1 = e.re_production_time
				this.value2 = e.re_expiration_time

				this.isexc = true

				let aa = e.re_goods_planting_picture.split(",")
				aa.pop()
				this.re_goods_planting_picture = aa
				

				let bb = e.re_goods_picture_detail.split(",")
				bb.pop()
				this.re_goods_picture_detail = bb

				this.re_goods_picture.push(e.re_goods_picture)

			}


		},
		mounted() {

		}
	}
</script>

<style>
	.inputwidth {
		width: 50%;
	}
</style>
