<template>
	<div class="typeinfo">
		
		
		<div class="bigbox any-flex">
			<div class="p_id">
				<ul>
					<li v-for="(item,index) in p_id_list" :class="index == active ? 'active' : '' " @click="p_idClick(item,index)"> <el-link type="_blank">{{item.t_name}}</el-link> </li>
				</ul>
			</div>
			
			<div class="t_id">
				<ul>
					<li v-for="(item,index) in t_id_list"> <el-link type="_blank">{{item.t_name}}  </el-link> <i @click="exchanges(item)" class="el-icon-edit hav"></i>	 </li>
					<li style="text-align: center;" @click="dialogVisible = true"><el-link type="_blank">+ 添加分类 </el-link></li>
				</ul>
			</div>
			
		</div>
		
		<el-dialog
		  title="提示"
		  :visible.sync="dialogVisible"
		  width="30%"
		  :before-close="handleClose">
		  
		  <div style="margin-bottom: 10px;">分类名称:</div>
		  <el-input v-model="t_name" placeholder="请输入分类名称"></el-input>
		  
		  <div  style="margin-top: 10px;">分类图片:</div>
		  <div class="any-flex">
			  
		  	<div v-for='(item,index) in fwzttp1' style="margin: 10px;">
		  		<el-image style="width: 50px; height: 50px" :src="imgurls + item" :preview-src-list='[imgurls + item]' fit="fill"></el-image>
		  		<!-- 图片上传 -->
		  		<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess2'
		  		 :limit="1" :file-list='fileList'>
		  			<el-button size="small" type="primary">点击修改或上传新图</el-button>
		  		</el-upload>
		  	</div>
		  </div>
		  
		  
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="admin_typeAdd()">确 定</el-button>
		  </span>
		</el-dialog>
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				imgurls:this.global.imgurl,
				fileList: [],
				fwzttp1:['/images/1574159410646407225.png'],
				t_name:'',
				isexsh:false,
				
				t_id:'',
				
				p_id:'',
				any_id_list:[],
				p_id_list:[],
				active:0,
				t_id_list:[],
				 dialogVisible: false,
			}
		},
		methods:{
			exchanges(e){
				this.dialogVisible = true
				this.t_id = e.t_id
				this.t_name = e.t_name
				this.fwzttp1 =[e.t_img + ''] 
			},
			//修改主图1
			fellsuccess2(response, file, fileList) {
				this.fileList = []
				let a = this.fwzttp1
				a[response.url_name] = response.path + ''
				this.fwzttp1 = a
				this.isexsh = true
			},
			// 增加/修改 二级分类
			admin_typeAdd(){
				
				if(this.isexsh){
					//修改
					this.utils.post({
						url:'admin_typeUpdate',
						data:{
						t_id:this.t_id,
					    t_name:this.t_name,
					    t_img:this.fwzttp1.join()
						}
					}).then(res=>{
						
						this.t_id_list.forEach((item,index)=>{
								if(this.t_id == item.t_id){
									item.t_name = this.t_name
								}
						})
						this.dialogVisible = false
						
					})
					
					
					
					
				}else{
					// 增加
					this.utils.post({
						url:'admin_typeAdd',
						data:{
							p_id:this.p_id,
							t_name:this.t_name,
							t_img:this.fwzttp1.join()
						}
					}).then(res=>{
						this.t_id_list.push({
							p_id:this.p_id,
							t_name:this.t_name,
							t_img:this.fwzttp1.join()
						})
						this.t_name = ''
						this.fwzttp1 = ['/images/1574159410646407225.png']
						this.dialogVisible = false
					})
				}

				
			},
			handleClose(done) {
			        this.$confirm('确认关闭？')
			          .then(_ => {
			            done();
			          })
			          .catch(_ => {});
			      },
			p_idClick(a,b){
				this.active = b
				this.p_id = a.t_id
				let p = []
				this.any_id_list.forEach((item,index)=>{
					if(  item.p_id == a.t_id){
						p.push(item)
					}
				})
				this.t_id_list = p
			},
			init(){
				this.utils.post({
					url:'admin_typeInfo'
				}).then(res=>{
					// 分类总列表
					this.any_id_list = res
					// 大分类数组
					let a = []
					res.forEach((item,index)=>{
						if(item.p_id == 0){
							a.push(item)
						}
					})
					this.p_id_list = a
					// 子分类默认
					let b = []
					res.forEach((item,index)=>{
						if(  item.p_id == a[0].t_id){
							b.push(item)
						}
					})
					
					this.p_id = a[0].t_id
					this.t_id_list = b
					
					
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
ul{
	list-style: none;padding:0;margin:0;
}
	.bigbox{
		width: 100%;
		.p_id{
			width:15%;
			height: 600px;
			margin: 50px;
			// border: 1px solid #333;
			overflow-y: scroll;
			overflow-x: hidden;
			border-radius: 10px;
			background: #69D6B5;
			&::-webkit-scrollbar{
							display: none;
			}
			li{
				text-align: center;
				padding: 10px 2px;
				color:#fff;
			}
			li:hover{
				background: #8694E7;
				color:#fff;
			}
			li:nth-child(1){
				margin-top: 20px;
			}
			.active{
				color:#8694E7;
			}
		}
		.t_id{
			width:25%;
			height: 600px; 
			margin: 50px;
			overflow-y: scroll;
			overflow-x: hidden;
			border-radius: 10px;
			background: #69D6B5;
			box-sizing: border-box;
			&::-webkit-scrollbar{
							display: none;
			}
			// padding:20px 40px;
			li:nth-child(1){
				margin-top: 20px;
				
			}
			li{
				// text-align: left;
				text-indent:25px;
				padding: 10px 2px;
				color:#fff;
				.hav:hover{
					color:#666 ;
				}
			}
			li:hover{
				background: #8694E7;
				color:#fff;
			}
			.active{
				color:#8694E7;
			}
		}
	}
</style>
