<template>
	<div class="goods">
		<div class="any-flex">
			<el-button type="primary">服务列表</el-button>
			<el-button type="success" @click='goaddgoods' v-if="this.global.is_shop()">新增服务</el-button>
		</div>

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="goods_id" label="服务ID" width="80">
			</el-table-column>
			<el-table-column prop="shop_name" label="商户名字" width="180" v-if="this.global.is_admin()">
			</el-table-column>
			<el-table-column prop="goods_name" label="服务名字" width="280">
			</el-table-column>
			<el-table-column prop="price" label="服务价格" width="80">
			</el-table-column>
			<el-table-column prop="goods_effect" label="服务功效" width="180">
			</el-table-column>

			<el-table-column prop="goods_duration" label="服务时长(min)" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_process" label="服务流程" width="180" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_frequency" label="服务次数" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_overdue_time" label="有效期(天)" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_appointment" label="预约提醒" width="180" v-if="this.global.is_admin()">
			</el-table-column>


			<el-table-column prop="" label=" 服务图片" width="180">
				<template slot-scope="scope">
					<el-image style="width: 169px; height: 97px" :src="imgurl + scope.row.picture" :preview-src-list="scope.row.srclist">
					</el-image>
				</template>
			</el-table-column>
			<el-table-column prop="goods_gd_num" label="出售数量" width="100">
			</el-table-column>

			<el-table-column prop="" label="活动操作" width="280" v-if="this.global.is_shop()">
				<template slot-scope="scope">
					<el-button type="primary" @click='promotion(scope.row)'>{{scope.row.is_promotion == 0 ? '开启拼团' :'关闭拼团'}}</el-button>
					<el-button type="primary" @click='limited_buys(scope.row)'>{{scope.row.limited_buy == 0 ? '开启限时抢' : '关闭限时抢'}}</el-button>
				</template>
			</el-table-column>
			<el-table-column prop="" label="操作" width="280">
				<template slot-scope="scope">
					<el-button type="success" @click='exchgoods(scope.row.goods_id)' v-if="isshop">修改服务</el-button>
					<el-button type="danger" @click='deletegoods(scope.row.goods_id,scope.$index)'>移除服务</el-button>
				</template>
			</el-table-column>

			<!--拼团 抽屉 -->
			<el-drawer title="开启拼团" :visible.sync="drawer" direction="ltr" custom-class="demo-drawer" :before-close="handleClose">
				<div class="demo-drawer__content">
					<div style="margin: 20px;"></div>
					<el-form :label-position="labelPosition" label-width="100px" :model="formLabelAlign">
						<el-form-item label="拼团价格">
							<el-input v-model="formLabelAlign.promotion_price"></el-input>
						</el-form-item>
<!-- 						<el-form-item label="拼团人数">
							<el-input v-model="formLabelAlign.promotion_prople"></el-input>
						</el-form-item> -->
					</el-form>
					<div class="any-flex" style="justify-content: space-between;margin-top: 50px;">
						<div></div>
						<el-button type="success" @click='goclick'>开启拼团</el-button>
					</div>
				</div>
			</el-drawer>

			<!--限时抢 抽屉 -->
			<el-drawer title="开启限时抢" :visible.sync="drawer1" direction="ltr" custom-class="demo-drawer" :before-close="handleClose">
				<div class="demo-drawer__content">
					<div class="addbooking">
						<div style="margin: 20px;"></div>
						<el-form :label-position="labelPosition" label-width="130px" :model="formLabelAlign1">
							<el-form-item label="限时抢价格">
								<el-input v-model="formLabelAlign1.limited_price"></el-input>
							</el-form-item>
							<el-form-item label="限时抢人数">
								<el-input v-model="formLabelAlign1.limited_prople"></el-input>
							</el-form-item>
							<el-form-item label="开始结束时间">
								<div class="block">
									<el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期"
									 end-placeholder="结束日期">
									</el-date-picker>
								</div>
							</el-form-item>
						</el-form>

						<div class="any-flex" style="justify-content: space-between;margin-top: 50px;">
							<div></div>
							<el-button type="success" @click='addlimit'>添加限时抢</el-button>
						</div>
					</div>
				</div>
			</el-drawer>
		</el-table>


		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>


	</div>
</template>

<script>
	export default {
		name: 'goods',
		data() {
			return {
				//限时抢
				drawer1: false,
				value1: [new Date(2019, 11, 10, 10, 10), new Date(2019, 11, 10, 10, 10)],
				formLabelAlign1: {
					limited_prople: '',
					limited_price: ''
				},
				///////-拼团----
				labelPosition: 'left',
				formLabelAlign: {
					promotion_price: '',
					promotion_prople: '1'
				},
				goods_id: '',
				// 抽屉的开关
				drawer: false,
				currentPage: 0,
				total: 0,
				pagesize: 0,
				a: this.global.url,
				dialogImageUrl: '',
				dialogVisible: false,
				tableData: [],
				imgurl:this.global.imgurl
			}
		},
		computed: {
			isshop:function(){
				 return this.global.is_shop()
			},
		},
		methods: {
			// 移除
			deletegoods(e,a){
				
				this.$confirm('此操作将永久移除, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {
							this.utils.post({
								url:'goodsdelete',
								data:{
									goods_id:e
								}
							}).then(res=>{

								this.tableData.splice(a,1)
								
							})
							
				        
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消删除'
				          });          
				        });
				
				

			},
			// 去添加
			goaddgoods() {
				this.$router.push({
					path: '/hellow/addgoods',
					query: {
						addgoods: 789,
						// goods_id:''
					}
				});
			},
			// 去修改
			exchgoods(e){
				this.$router.push({
					path: '/hellow/addgoods',
					query: {
						addgoods: 456,
						goods_id:e
					}
				});
			},
			// 抽屉关了
			handleClose(done) {
				done();
				this.formLabelAlign.promotion_price = '',
					this.formLabelAlign.promotion_prople = '1',
					this.goods_id = ''
			},
			//点击添加限时抢
			addlimit() {
				this.formLabelAlign1.goods_id = this.goods_id,

					this.formLabelAlign1.limited_start_time = new Date(this.value1[0]).getTime()/1000 ,
					this.formLabelAlign1.limited_stop_time = new Date(this.value1[1]).getTime()/1000,
					this.formLabelAlign1.limited_buy = 1

				this.utils.post({
					url: 'admin_Limited',
					data: this.formLabelAlign1
				}).then(res => {
						this.tableData.forEach((item, index) => {
							if (item.goods_id == this.goods_id) {
								if(item.is_promotion == 0){
									item.limited_buy = 1
								}
							}
						})
					
					this.drawer1 = false
				})
			},
			// 点击开启限时抢
			limited_buys(e) {
				this.goods_id = e.goods_id
				if (e.limited_buy == 0) {
					this.drawer1 = true
				} else if (e.limited_buy == 1) {
					this.utils.post({
						url: 'admin_Limited',
						data: {
							goods_id: this.goods_id,
							limited_buy: 0
						}
					}).then(res => {
						
							this.tableData.forEach((item, index) => {
								if (item.goods_id == this.goods_id) {
									item.limited_buy = 0
								}
							})
						
					})
				}
			},
			// 点击拼团按钮
			promotion(e) {
				this.goods_id = e.goods_id
				if (e.is_promotion == 0) {
					this.drawer = true
				} else if (e.is_promotion == 1) {
					this.utils.post({
						url: 'admin_Assemble',
						data: {
							is_promotion: 0,
							goods_id: this.goods_id
						}
					}).then(res => {
							this.tableData.forEach((item, index) => {
								if (item.goods_id == this.goods_id) {
									item.is_promotion = 0
								}
							})
					})
				}

			},
			// 开启拼团
			goclick() {
				
				this.formLabelAlign.goods_id = this.goods_id
				this.formLabelAlign.is_promotion = 1
				this.global.a(this.formLabelAlign)
				this.utils.post({
					url: 'admin_Assemble',
					data: this.formLabelAlign
				}).then(res => {
					this.tableData.forEach((item, index) => {
						if (item.goods_id == this.goods_id) {
							if(item.limited_buy == 0){
								item.is_promotion = 1
							}
						}
					})
					this.drawer = false
				})
			},
			// --- 图片
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 请求列表
			init() {
				this.utils.post({
					url: 'goodsList',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					res.data.forEach((item, index) => {
						item.srclist = [this.imgurl + item.picture]
					})
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					this.tableData = res.data
				})
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'goodsList',
					data: {
						page: e,
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					res.data.forEach((item, index) => {
						item.srclist = [this.imgurl + item.picture]
					})
					this.tableData = res.data
				})

			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}

	.demo-drawer__content {
		width: 90%;
		margin: 0 auto;
	}
</style>
