<template>
	<div class="myorder">
		
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="re_order_id" label="订单ID" width="180">

			</el-table-column>
			<el-table-column prop="re_order_no" label="订单编号" width="180">
			</el-table-column>
			<el-table-column prop="create_time" label="订单时间">
			</el-table-column>

			<el-table-column prop="buy_num" label="购买数量">
			</el-table-column>

			<el-table-column prop="re_goods_name" label="商品名称">
			</el-table-column>

			<el-table-column prop="re_goods_price" label="订单价格">
			</el-table-column>

			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					  <el-button type="primary" @click="fahuo(scope.row.re_order_id , scope.row.order_status)"> {{ scope.row.order_status == 1 ? '发货' : '查看物流' }} </el-button>
				</template>
			</el-table-column>

		</el-table>

		<el-dialog  title="收货地址" width='55%' :visible.sync="dialogFormVisible">
			
		  <el-form :model="form" v-if='status'>
			  <el-form-item label="物流公司" :label-width="formLabelWidth">
				<el-select v-model="form.log_id" placeholder="请选择物流公司">	
				  <el-option v-for="(item,index) in type_from" :label="item.log_name" :value="item.log_id"></el-option>
				</el-select>
			  </el-form-item>
			<el-form-item label="物流单号" :label-width="formLabelWidth">
			  <el-input v-model="form.log_num" style='width:300px;' autocomplete="off"></el-input>
			</el-form-item>
		  </el-form>
		  
		  <div v-else>
			  
			<!-- <div class="wl" v-for="(item,index) in logistics" :key='index'> -->
<!-- 					<div>{{item.AcceptStation}}</div>
					<div>{{item.AcceptTime}}</div> -->
					
					<div style="height: 600px;">
					  <el-steps direction="vertical" :active="logistics.length">
						 
					    <el-step v-for="(item,index) in logistics" :key='index' :title="item.AcceptTime" :description='item.AcceptStation'></el-step>

					  </el-steps>
					</div>
			<!-- </div> -->
			  
		  </div>
		  
		  <div slot="footer" class="dialog-footer">
			<el-button @click="dialogFormVisible = false">取 消</el-button>
			<el-button type="primary" @click="fabuwuliu">确 定</el-button>
		  </div>
		</el-dialog>


		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				status:true,
				currentPage: 0,
				total: 0,
				pagesize: 0,
				tableData: [],
				dialogFormVisible: false,
				form: {
		           log_num: '',
		           log_id: '',
				   re_order_id:0
				},
				formLabelWidth: '120px',
				type_from:[],
				//物流
				logistics:[]
			}
		},
		methods: {
			// 发布物流
			fabuwuliu(){
				
				this.utils.post({
					url:'admin_reseller_Confirm_shipment',
					data:this.form
				})
				
			this.dialogFormVisible = false
			},

			//发货 或者 查看物流
			fahuo(e,a){
				this.logistics = []
				this.status = a == 1 ? true : false ;
				this.dialogFormVisible = true
				if(a == 1){
					this.form.re_order_id = e
					this.utils.post({
						url:'admin_Logistics_type'
					}).then(res=>{
						this.type_from = res
					})
					
				}else{
					this.utils.post({
						url:'admin_reseller_order_information',
						data:{
							re_order_id:e
						}
					}).then(res=>{
						
						this.global.a( JSON.parse(res) )
						this.logistics = JSON.parse(res).Traces
						
					})
				}
				
				
			},
			
			init() {
				this.utils.post({
					url: 'admin_reseller_order',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					res.data.forEach((item,index)=>{
						item.create_time = this.global.timelaver(item.create_time * 1000)
					})
					this.tableData = res.data
				})
			},
			changepages(e){
				this.global.a({a:'下一页',ss:e})
				this.utils.post({
					url: 'admin_reseller_order',
					data: {
						shop_id: this.global.shop_id(),
						page:e
					}
				}).then(res => {  
					res.data.forEach((item,index)=>{
						item.create_time = this.global.timelaver(item.create_time * 1000)
					})
					this.tableData = res.data
				})
			}
			
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
	.wl{
		margin: 5px;
		padding: 5px;
		box-sizing: border-box;
		width: 100%;
		height: 80px;
	}
</style>
