<template>
	<div class="account">
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="admin_user" label="账号" width="180">
			</el-table-column>
			<el-table-column prop="shop_name" label="商铺名称" width="180">
			</el-table-column>
			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					<el-button type="primary" @click='stop(scope.row)'>{{scope.row.shop_status== 2 ? '停用' : '启用'}}  </el-button>
					<el-button type="danger" @click='deletes(scope.row,scope.$index)'>删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		
		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>
		
	</div>
</template> 

<script>
	export default {
		data() {
			return {
				tableData: [],
				currentPage: 0,
				total: 0,
				pagesize: 0,
			}
		},
		methods:{
			stop(e){
				if(e.shop_status == 2){
					this.ausot(e,1)
				}else{
					this.ausot(e,2)
				}
			},
			ausot(e,a){
			
				this.utils.post({
					url:'admin_black',
					data:{
						admin_id:e.admin_id,
						shop_status:a,
						shop_id:e.shop_id
					}
				}).then(res=>{

					this.tableData.forEach((item,index)=>{
						if(e.admin_id == item.admin_id){
							item.shop_status = a
						}
					})
					
					
					
				})
			},

			deletes(e,a){
				this.$confirm('此操作将永久删除该用户相关联的所有信息, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).then(() => {

					this.utils.post({
						url:'admin_delete',
						data:{
							admin_id:e.admin_id,
							shop_id:e.shop_id
						}
					}).then(res=>{
						this.tableData.splice(a,1)
					})

				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: '已取消删除'
				          });          
				        });
				
				

			},
			init(){
				this.utils.post({
					url:'admin_list'
				}).then(res=>{
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					
					this.tableData = res.data
				})
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'admin_list',
					data: {
						page: e
					}
				}).then(res => {
					this.tableData = res.data
				})
			
			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style>
	.account{
			padding: 10px 0 0 5px;
		}
		.pagein {
			padding: 20px 90px;
			justify-content: space-between;
		}
</style>
