<template>
	<div class="addenter">

		<div style="margin: 20px;"></div>
		<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
			<el-form-item label="店铺名">
				<el-input v-model="formLabelAlign.shop_name"></el-input>
			</el-form-item>
			<el-form-item label="商家电话">
				<el-input v-model="formLabelAlign.shop_phone"></el-input>
			</el-form-item>
			<el-form-item label="省">
				<el-input v-model="formLabelAlign.shop_address_provice"></el-input>
			</el-form-item>
			<el-form-item label="市">
				<el-input v-model="formLabelAlign.shop_address_city"></el-input>
			</el-form-item>
			<el-form-item label="区">
				<el-input v-model="formLabelAlign.shop_address_area"></el-input>
			</el-form-item>
			<el-form-item label="详细地址">
				<el-input v-model="formLabelAlign.shop_address_detail"></el-input>
			</el-form-item>
			<el-form-item label="经纬度">
				<div class="map-baidu">
					<baidu-map class="map" :zoom="1" :scroll-wheel-zoom='true'>
						<bm-geolocation :showAddressBar="true" :autoLocation="true"></bm-geolocation>
						<bm-local-search @infohtmlset='bztjwc' :panel='false' :autoViewport='true'  :selectFirstResult='true' :keyword="formLabelAlign.shop_address_detail"
						 :auto-viewport="true" :location="formLabelAlign.shop_address_city">
						</bm-local-search>
					</baidu-map>
				</div>
			</el-form-item>
			<el-form-item label="联系人">
				<el-input v-model="formLabelAlign.shop_contacts"></el-input>
			</el-form-item>

			<el-form-item label="证书编号">
				<el-input v-model="formLabelAlign.shop_certificate"></el-input>
			</el-form-item>

			<el-form-item label="身份证号">
				<el-input v-model="formLabelAlign.shop_sfz"></el-input>
			</el-form-item>

			<el-form-item label=" 主营项目">
				<el-input v-model="formLabelAlign.t_name"></el-input>
			</el-form-item>

				<div class="any-flex">
					<!-- <div style="width: 10%;"></div> -->
					<el-button type="success" @click='pass' round>通过审核</el-button>
					
				</div>



		</el-form>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				labelPosition: 'left',
				formLabelAlign: {
					shop_certificate: '',
					shop_sfz:'',
					// latitude_longitude: {
					// 	lat: '',
					// 	lng: ''
					// },
					shop_address_area: "",
					shop_address_city: "",
					shop_address_detail: "",
					shop_address_provice: "",
					shop_contacts: "",
					shop_id: 0,
					shop_name: "",
					shop_phone: "",
					t_name: '',
					shop_id:''
				}
			};
		},
		methods: {

			bztjwc(e) {
				// this.formLabelAlign.latitude_longitude = e.point
				this.formLabelAlign.lat = e.point.lat
				this.formLabelAlign.lng = e.point.lng
				
			},
			pass(){
				
				let ass = this.formLabelAlign
				
				if(ass.shop_certificate == ''){
					 this.$message({
					          message: '请填写营业执照证书编号',
					          type: 'warning'
					        });
				}else if(ass.shop_sfz == '' ){
					this.$message({
					         message: '请填写法人的身份证号',
					         type: 'warning'
					       });
				}else{
					this.global.a(this.formLabelAlign)
					this.utils.post({
						url:'examine',
						data:this.formLabelAlign
						
					}).then(res=>{
						this.$router.push("/hellow/enter")
					})
				}
							
			}
		},
		created() {
			let e = this.$route.query.name
			this.formLabelAlign.shop_address_area = e.shop_address_area
			this.formLabelAlign.shop_address_city = e.shop_address_city
			this.formLabelAlign.shop_address_detail = e.shop_address_detail
			this.formLabelAlign.shop_address_provice = e.shop_address_provice
			this.formLabelAlign.shop_contacts = e.shop_contacts
			this.formLabelAlign.shop_id = e.shop_id
			this.formLabelAlign.shop_name = e.shop_name
			this.formLabelAlign.shop_phone = e.shop_phone
			this.formLabelAlign.t_name = e.t_name
			this.location = e.shop_address_city
			this.keyword = e.shop_address_detail
		}
	}
</script>

<style lang="scss">
	.map-baidu {
		width: 100%;
		height: 300px;

		// background: #aa6548;
		.map {
			width: 100%;
			height: 300px;
		}
	}
</style>
