<template>
	<div class="distribution">

		<div class="bigbox">
			
			<div v-if='isshtg==0' class="contents">
				您还不是分销商，请申请后再使用本功能。 <el-link type="primary" @click='succes'>您可以点此申请</el-link>
			</div>
			<div class="contents" v-if='isshtg==2'>
				您的申请在审核中，审核通过后才可以使用此功能。。。
			</div>
			
			
		</div>

		<div class="nobig any-flex" v-if='isshtg==1'>

			<div class="father any-flex" @click="navigator('/myteam')">
				<div class="font">
					<el-image style="width: 100px; height: 100px" :src="url" fit="fill"></el-image>
				</div>
				<div class="text"> 
					<div class="tit">我的团队</div>
					<div class="mes">我的团队中包括我的分销团队中所有成员</div>
				 </div>
			</div>
			
			<div class="father any-flex" @click="navigator('/mygoods')">
				<div class="font">
					<el-image style="width: 100px; height: 100px;" :src="url" fit="fill"></el-image>
				</div>
				<div class="text"> 
					<div class="tit">我的商品</div>
					<div class="mes">分销商城所展示的所有商品</div>
				 </div>
			</div>

			<div class="father any-flex" @click="navigator('/myorder')">
				<div class="font">
					<el-image style="width: 100px; height: 100px" :src="url" fit="fill"></el-image>
				</div>
				<div class="text"> 
					<div class="tit">我的订单</div>
					<div class="mes">分销商城所销售的订单</div>
				 </div>
			</div>

<!-- 			<div class="father any-flex">
				<div class="font">
					<el-image style="width: 100px; height: 100px" :src="url" fit="fill"></el-image>
				</div>
				<div class="text"> 
					<div class="tit">收货/发货</div>
					<div class="mes">收货的地址配置和给用户发货</div>
				 </div>
			</div> -->

			<div class="father any-flex" @click="dialogFormVisible = true">
				<div class="font">
					<el-image style="width: 100px; height: 100px" :src="url" fit="fill"></el-image>
				</div>
				<div class="text"> 
					<div class="tit">返利调整</div>
					<div class="mes">调整分销返利的百分比</div>
				 </div>
			</div>
			<!-- 弹窗 -->
			<el-dialog title="收货地址" :visible.sync="dialogFormVisible">
			  <el-form :model="form">
				<el-form-item label="直接上级返利( 单位 % 仅保留一位小数)" :label-width="formLabelWidth">
				  <el-input style='width: 40%;' v-model="form.up_rebate" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="间接上级返利(单位 % 仅保留一位小数)" :label-width="formLabelWidth">
				  <el-input style='width: 40%;' v-model="form.indirect_up_rebate" autocomplete="off"></el-input>
				</el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="ecfenxiao">确 定</el-button>
			  </div>
			</el-dialog>

		</div>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				dialogFormVisible: false,
				        form: {
				          up_rebate: '',
				          indirect_up_rebate: '',
							shop_id : this.global.shop_id()
				        },
				        formLabelWidth: '280px',
				
				url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
				isshtg: '0' //1分销商 2. 审核中 0普通用户
			}
		},
		methods: {
			// 调整返利
			ecfenxiao(){
				this.utils.post({
					url:'admin_set_rebate',
					data:this.form
				}).then(res=>{
					this.dialogFormVisible = false
				})
				
				
			},
			
			navigator(e){
				this.$router.push(e)
			},
			succes() {
				
				this.utils.post({
					url: 'admin_apply_reseller',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then((res)=>{
					this.isshtg = '2'
				})

			},
			// 查看返利比例
			admin(){
				
				this.utils.post({
					url:'admin_rebate_detail',
					data:{
						shop_id:this.global.shop_id()
					}
				}).then(res=>{
					this.isshtg = res.shop_reseller
					this.form.up_rebate = res.up_rebate
					this.form.indirect_up_rebate = res.indirect_up_rebate
				})
				
			}
			
		},
		mounted() {
			this.admin()
		}
	}
</script>

<style lang="scss">
	.bigbox {
		width: 100%;
		// height: 100vh;
		overflow: hidden;

		.contents {
			text-align: center;
			width: 50%;
			margin: 50px auto;
		}
	}

	.nobig {
		flex-wrap: wrap;
		.father {
			width: 380px;
			height: 150px;
			// background: red;
			border-radius: 10px;
			margin: 15px;
			border: 1px solid #ccc;
			.font {
				margin: 0 20px;
				
				.el-image{
					border-radius:50%; ;
				}
			}

			.text {
				.tit{
					font-weight: 600;
					// text-align: center;
				}
				.mes{
					font-size: 8px;
					text-align: left;
				}
				
			}
		}

	}
</style>
