<template>
	<div>
		<div class="anypeople">
			团队总人数：{{team_num}} 人		
		</div>
		
		
		  
		    <el-table
		      :data="tableData"
		      style="width: 100%">
		      <el-table-column
		        prop=""
		        label="头像"
		        width="180">
				
				<template slot-scope="scope">
					
					 <el-image
					      style="width: 50px; height: 50px"
					      :src="scope.row.wx_headimg"
					      fit="fill"></el-image>
					  
					
				</template>
				
		      </el-table-column>
		      <el-table-column
		        prop="wx_name"
		        label="昵称"
		        width="180">
		      </el-table-column>

		    </el-table>
		 
		
		
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				 tableData: [],
				 team_num:''
			}
		},
		methods:{
			init(){
				this.utils.post({
					url:'admin_my_team',
					data:{shop_id:this.global.shop_id()}
				}).then(res=>{
					this.global.a(res)
					this.tableData = res.teamInfo
					this.team_num = res.total_num
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.anypeople{
		color: #666;
		padding: 10px;
	}
</style>
