<template>
	<div class="addcoupon">
		
		新增优惠券
		
		<div style="margin: 20px;"></div>
		<el-form label-position="left" label-width="100px" :model="formLabelAlign">
		  <el-form-item label="优惠券类型">
		     <el-select v-model="value" placeholder="请选择" @change='changesome'>
		       <el-option
		         v-for="item in options"
		         :key="item.value"
		         :label="item.label"
		         :value="item.value">
		       </el-option>
		     </el-select>
		  </el-form-item>
		  
		  <el-form-item label="选择商品">
			  <el-radio-group v-model="radio">
				
				<el-radio class='radiosa' v-for="(item,index) in goods_list" :label="item.goods_id">{{item.goods_name}}</el-radio>
			  </el-radio-group>
		   
		  </el-form-item>
		  
		  <el-form-item label="格式/折扣">
			<div class="any-flex" v-if='mytypes == 0'>
				满
				<el-input style='width: 100px;' v-model="formLabelAlign.coupon_redouction"></el-input>
				减
				<el-input style='width: 100px;' v-model="formLabelAlign.coupon_price"></el-input>
			</div>
			
			<div v-if='mytypes == 1'>
				<el-input style='width: 100px;' v-model="formLabelAlign.is_member_discount"></el-input>
			</div>
			
		  </el-form-item>
		  
		  <el-form-item label="优惠券名称">
		    <el-input v-model="formLabelAlign.coupon_names"></el-input>
		  </el-form-item>
		  <el-form-item label="数量">
		    <el-input v-model="formLabelAlign.coupon_num"></el-input>
		  </el-form-item>

		  <el-form-item label="使用期限">
		        <el-date-picker
		          v-model="value1"
				  value-format='timestamp'
		          type="daterange"
		          range-separator="至"
		          start-placeholder="开始日期"
		          end-placeholder="结束日期">
		        </el-date-picker>
		  </el-form-item>
		  
		  
		    <el-button @click='addcoupon' type="primary">确认添加</el-button>
		</el-form>
		
	</div>
</template>

<script>
  export default {
    data() {
      return {
        formLabelAlign: {
		  // 类型
		  coupon_type:'',
		  //goods_id
	
		  //优惠券名字
          coupon_names: '',
		  // 优惠券数量
          coupon_num: '',
		  //满 --
          coupon_redouction: '',     
		  //减 -- 
		  coupon_price:'',
		  //折扣
		  is_member_discount:'',
		  
		  
        },
		goods_list:[],
		radio: 0,
		mytypes:'0',
		value1: '',
		 options: [{
		          value: '1',
		          label: '折扣券'
		        }, {
		          value: '0',
		          label: '满减券'
		        }],
		        value: '',
				goods_prices:''
      };
    },
	methods:{

		
		// 选择优惠券类型
		changesome(e){
			this.formLabelAlign.coupon_type = e
			this.mytypes = e
		},
		init(){
			// 获取商品列表
			this.utils.post({
				url:'coupon',
				data:{shop_id :this.global.shop_id()}
			}).then(res=>{
				this.goods_list = res.data
			})
		},
		addcoupon(){
			this.formLabelAlign.shop_id  =  this.global.shop_id()
			this.formLabelAlign.goods_id  =  this.radio
			this.formLabelAlign.coupon_start_time = this.value1[0]/1000
			this.formLabelAlign.expiration = this.value1[1]/1000
			// radio
			this.goods_list.forEach((item,index)=>{
				if(item.goods_id == this.formLabelAlign.goods_id){
					this.goods_prices = item.price
				}
			})
			this.global.a(this.goods_prices)
			if( this.goods_prices <= this.formLabelAlign.coupon_redouction ){
				return this.$message.error('满减价格不能超出商品价格');
			}
			
			this.utils.post({
				url:'couponinsert',
				data:this.formLabelAlign
			}).then(res=>{
				this.$router.push('/hellow/coupon')
			})
			
		}
	},
	mounted() {
		this.init()
	}
  }
</script>

<style lang="scss">
	
	.radiosa{
		margin: 10px;
	}
</style>
