<template>
	<div>

		<el-tabs v-model="activeName" @tab-click="handleClick">
			<el-tab-pane label="分销代理" name="first">

				<el-table :data="tableData" style="width: 100%">
					<el-table-column prop="shop_name" label="店铺名称" width="180">
					</el-table-column>
					<el-table-column prop="contacts" label="申请人" width="180">
					</el-table-column>
					<el-table-column prop="contacts_tel" label="联系方式" width="180">
					</el-table-column>
					<el-table-column prop="shop_address" label="地址">
					</el-table-column>
				</el-table>


		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>



			</el-tab-pane>
			<el-tab-pane label="分销商" name="second">

				<el-table :data="tableData1" style="width: 100%">
					<el-table-column prop="shop_name" label="店铺名称" width="180">
					</el-table-column>
					<el-table-column prop="contacts" label="申请人" width="180">
					</el-table-column>
					<el-table-column prop="contacts_tel" label="联系方式" width="180">
					</el-table-column>
					<el-table-column prop="shop_address" label="地址">
					</el-table-column>
				</el-table>
				
				<div class="any-flex pagein">
					<div></div>
					<el-pagination background prev-text='上一页' :total='total1' next-text='下一页' layout="prev, pager, next" @current-change='changepages1'
					 :current-page='currentPage1' :page-size='pagesize1'>
					</el-pagination>
				</div>

			</el-tab-pane>
			<el-tab-pane label="异业联盟" name="third">

				<el-table :data="tableData2" style="width: 100%">
					<el-table-column prop="industry" label="所属行业" width="180">
					</el-table-column>
					<el-table-column prop="contacts" label="申请人" width="180">
					</el-table-column>
					<el-table-column prop="contacts_tel" label="联系方式" width="180">
					</el-table-column>
					<el-table-column prop="remarks" label="备注">
					</el-table-column>
				</el-table>

				<div class="any-flex pagein">
					<div></div>
					<el-pagination background prev-text='上一页' :total='total2' next-text='下一页' layout="prev, pager, next" @current-change='changepages2'
					 :current-page='currentPage2' :page-size='pagesize2'>
					</el-pagination>
				</div>

			</el-tab-pane>
		</el-tabs>


	</div>
</template>

<script>
	export default {
		data() {
			return {
				activeName: 'second',
				tableData: [],
				tableData1: [],
				tableData2: [],
				
				currentPage: 0,
				total: 0,
				pagesize: 0,
				page:1,
				
				currentPage1: 0,
				total1: 0,
				pagesize1: 0,
				page1:1,
				
				currentPage2: 0,
				total2: 0,
				pagesize2: 0,
				page2:1
			}
		},
		methods: {
			handleClick(tab, event) {
				console.log(tab, event);
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 1,
						page:this.data.page
					}
				}).then(res => {
					this.tableData = res.data
				})
			
			},
			
			// 分页
			changepages1(e) {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 1,
						page:this.data.page1
					}
				}).then(res => {
					this.tableData1 = res.data
				})
			
			},
			// 分页
			changepages2(e) {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 1,
						page:this.data.page2
					}
				}).then(res => {
					this.tableData2 = res.data
				})
			
			},
			
			init() {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 1
					}
				}).then(res => {
					this.tableData = res.data
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
				})
			},
			init1() {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 2
					}
				}).then(res => {
					this.tableData1 = res.data
					
					this.total1 = res.total
					this.currentPage1 = res.current_page
					this.pagesize1 = res.per_page

				})
			},
			init2() {
				this.utils.post({
					url: 'admin_Application_reseller_list',
					data: {
						is_distribution: 3
					}
				}).then(res => {
					this.tableData2 = res.data
					
					this.total2 = res.total
					this.currentPage2 = res.current_page
					this.pagesize2 = res.per_page

				})
			}
		},
		mounted() {
			this.init()
			this.init1()
			this.init2()
		}
	}
</script>

<style>
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
</style>
