<template>
	<div class="case">
		<el-button type="primary">案例列表</el-button>
		<el-button type="success" @click='addcase'>新增案例</el-button>

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="goods_name" label="体验服务" width="180">
			</el-table-column>
			<el-table-column prop="case_front" label="体验前" width="180">

				<template slot-scope="scope">
					<el-image style="width: 100px; height: 100px" :src="imgurls + scope.row.case_front" :preview-src-list="[ imgurls + scope.row.case_front +'']"></el-image>
				</template>

			</el-table-column>
			<el-table-column prop="case_after" label="体验后">

				<template slot-scope="scope">
					<el-image style="width: 100px; height: 100px" :src="imgurls + scope.row.case_after" :preview-src-list="[ imgurls + scope.row.case_after +'']"></el-image>
				</template>

			</el-table-column>
			<el-table-column prop="case_trouble" label="问题">
			</el-table-column>

			<el-table-column prop="" label="操作">
				<template slot-scope="scope">
					<el-button type="warning" @click='exal(scope.row,scope.$index)'>修改案例</el-button>
					<el-button type="danger" @click='delate(scope.row,scope.$index)'>删除案例</el-button>

				</template>
			</el-table-column>
		</el-table>


		<el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">

			<!-- 选择器 -->
			<div class="any-flex" v-if="isadd">

				<div>选择服务：</div>
				<el-select v-model="value" placeholder="请选择" @change="hecha">
					<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
					</el-option>
				</el-select>

			</div>

			<div class="any-flex">
				<div v-for='(item,index) in fwlbt' style="margin: 10px;">
					<div v-if='index == 0'>体验前 </div>
					<div v-if='index == 1'>体验后</div>
					<el-image style="width: 180px; height: 100px" :src="imgurls + item" :preview-src-list='[imgurls + item]  ' fit="fill"></el-image>
					<!-- 图片上传 -->
					
					<el-upload class="upload-demo" :data='{ url_name: index }' action="upload" :show-file-list="false" :on-success='fellsuccess'
					 :limit="1" :file-list='fileList'>
						<el-button size="small" type="primary"> {{ isadd ? '点击上传' : '点击修改' }}</el-button>
						<!-- <el-button size="small" type="primary">点击上传</el-button> -->
					</el-upload>
				</div>
			</div>

			<!-- 简介 -->

			<div class="any-flex">
				<div>案例简介：</div>
				<el-input style='width: 70%;' v-model="input" placeholder="请输入内容"></el-input>
			</div>


			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="ecsome">确 定</el-button>
			</span>
		</el-dialog>



		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				options: [{
					value: '选项1',
					label: '黄金糕'
				}],
				isadd: false,
				goods_id: '',
				case_id: '',
				value: '',
				input: '',
				fwlbt: ['a', 'b'],
				fileList: [],
				imgurls: this.global.imgurl,
				tableData: [],
				currentPage: 0,
				total: 0,
				pagesize: 0,
				dialogVisible: false,
				imageurl: this.global.imgurl,
			}
		},
		methods: {
			// 选择器改变
			hecha(e) {
				this.goods_id = e
			},
			// 新增
			addcase() {
				this.dialogVisible = true
				this.isadd = true
				this.utils.post({
					url: 'admin_shop_goodsInfo',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					
					this.global.a(res)
					let a = []
					res.forEach((item, index) => {
						a.push({
							value: item.goods_id,
							label: item.goods_name
						})
					})
					this.options = a

				})


			},
			// 新增////--+++++--///提交修改
			ecsome() {
				if (this.isadd) {

					this.utils.post({
						url: 'admin_caseAdd',
						data: {
							shop_id: this.global.shop_id(),
							goods_id: this.goods_id,
							case_front: this.fwlbt[0] + '',
							case_after: this.fwlbt[1] + '',
							case_trouble: this.input
						}
					}).then(res => {
						this.fwlbt = ['a', 'b']
						this.input = ''
						this.dialogVisible = false
						this.isadd = false
					})



				} else {



					this.utils.post({
						url: 'admin_caseUpdate',
						data: {
							case_id: this.case_id,
							case_front: this.fwlbt[0] + '',
							case_after: this.fwlbt[1] + '',
							case_trouble: this.input
						}
					}).then(res => {
						this.tableData.forEach((item, index) => {
							if (this.case_id == item.case_id) {
								item.case_front = this.fwlbt[0] + '',
									item.case_after = this.fwlbt[1] + '',
									item.case_trouble = this.input
							}
						})
						this.fwlbt = ['a', 'b']
						this.input = ''
						this.dialogVisible = false
					})
				}
			},
			// 点击修改按钮
			exal(e, a) {
				this.dialogVisible = true
				let ss = []
				this.tableData.forEach((item, index) => {
					//体验前后的图
					if (a == index) {
						ss.push(item.case_front)
						ss.push(item.case_after)
						this.input = item.case_trouble
					}
				})
				this.case_id = e.case_id
				this.fwlbt = ss
			},
			// 改图
			fellsuccess(response, file, fileList) {
				this.fileList = []
				let a = this.fwlbt
				a[response.url_name] = response.path + ''
				this.fwlbt = a
			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						this.fwlbt = ['a', 'b']
						this.input = ''
						done();
					})
					.catch(_ => {});
			},
			delate(e, a) {
				this.$confirm('此操作将永久删除该案例, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {

					this.utils.post({
						url: 'admin_caseDelete',
						data: {
							case_id: e.case_id
						}
					}).then(res => {
						this.tableData.splice(a, 1)
					})

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			init() {
				this.utils.post({
					url: 'admin_caseList',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					this.global.a({
						a: '案例列表',
						res: res
					})

					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					this.tableData = res.data
				})
			},
			// 分页
			changepages(e) {
				this.utils.post({
					url: 'admin_caseList',
					data: {
						page: e,
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					this.tableData = res.data
				})

			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
</style>
