<template>
	<div class="recommend">
		<!-- 标题 -->
		<div class="tuijw">首页推荐位:</div>
		
		<!-- 大选项卡 -->
		<el-tabs v-model="activeName" @tab-click="handleClick">
			
			<!-- 轮播图的选项卡 -->
			<el-tab-pane label="轮播" name="first">

				<el-table :data="tableData" stripe style="width: 100%">
					<el-table-column prop="" label="位置" width="180">
						轮播图
					</el-table-column>
					<el-table-column prop="" label="第 * 张" width="180">
						<template slot-scope="scope">

							第{{scope.$index + 1}}张
						</template>
					</el-table-column>
					<el-table-column prop="" label="图片" width="380">
						<template slot-scope="scope">
							<div class="any-flex">
								<div>
									<el-image style="width: 150px; height: 80px" :show-file-list="false" :src="a + scope.row.rotation_img"
									 :preview-src-list="scope.row.srcList"></el-image>
									<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
								</div>
								<el-upload class="upload-demo" :data='{ rotation_id:scope.row.rotation_id }' action="imguptate" :on-preview="handlePreview"
								 :on-success='fellsuccess' :limit="1" :on-error='pageserro' :file-list="scope.row.firl">
									<el-button size="small" type="primary">点击修改</el-button>
								</el-upload>
							</div>

						</template>
					</el-table-column>
					<el-table-column prop="" label="路径" width="380">
						<template slot-scope="scope">

							<el-input v-model="scope.row.rotation_rou" :disabled="scope.row.blem"></el-input>
						</template>
					</el-table-column>
					<el-table-column prop="" label="id" width="180">
						<template slot-scope="scope">

							<el-input v-model="scope.row.rotation_net_id" :disabled="scope.row.blem">
							</el-input>

						</template>
					</el-table-column>

					<el-table-column prop="" label="操作" width="180">
						<template slot-scope="scope">
							<el-button size="small" type="primary" @click='exchangems(scope.row)'>{{scope.row.blem?"修改":"确认"}}</el-button>
						</template>
					</el-table-column>
				</el-table>


			</el-tab-pane>
			
			<!-- 今日推荐的选项卡 -->
			<el-tab-pane label="商品今日推荐" name="second">

				<el-table :data="tableData_goods" style="width: 100%">
					<el-table-column prop="" label="第*个" width="180">
						<template slot-scope="scope">
						第 {{scope.$index + 1}} 个商品推荐位 
						</template>
					</el-table-column>

					<el-table-column prop="" label="店铺名称" width="280">
						<template slot-scope="scope">
							<div class="any-flex someurl">
								<el-input style='width:70%' v-model="scope.row.shop_name" :disabled="scope.row.blem"></el-input>
								<el-button size="small" @click='searchto(scope.row)' type="primary" v-if = '!scope.row.blem'>查询</el-button>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="shop_goods" label="商品名称" width="380">
						<template slot-scope="scope">
							<el-select style='width: 90%;' @change='mychange' v-model="scope.row.value" :placeholder="scope.row.goods_name" :disabled="scope.row.blem">
								<el-option v-for="item in scope.row.options" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="goods_image" label="图片" width="180">
						<template slot-scope="scope">
						<el-image style="width: 82px; height: 72px" :show-file-list="false" :src="a + scope.row.picture"
						 :preview-src-list="scope.row.srcList"></el-image>
						 </template>
					</el-table-column>
					<el-table-column prop="price" label="价格" width="180">
					</el-table-column>
					<el-table-column prop="" label="操作" width="180">
						<template slot-scope="scope">
							<el-button size="small" type="primary" @click='exchgoodssome(scope.row)'>{{scope.row.blem?"修改":"确认"}}</el-button>
						</template>
					</el-table-column>


				</el-table>

			</el-tab-pane>
			

		</el-tabs>








	</div>
</template>

<script>
	export default {
		name: "home",
		data() {
			return {
				a: '',
				activeName: 'first',
				tableData_goods: [{
					shop_name: '王家店',
					shop_goods: '补水',
					goods_scripe: '120',
					goods_image: '100',

					somebul: true
				}],
			
				tableData: [],
				goods_id:''
			}

		},
		methods: {
			handlePreview(file) {
				console.log(file);
			},
			// 上传完成后清理列表
			fellsuccess(res, file, filelist) {
				this.tableData.forEach((item, index) => {
					if (item.rotation_id == res.data.rotation_id) {
						setTimeout(()=>{
							item.rotation_img = res.data.rotation_img
							item.srcList = ['' + res.data.rotation_img]
							item.firl = []
						},3000)
						
					}
				})
			},
			// 文件上传失败
			pageserro(res) {
				this.$message.error('文件上传失败！');
			},
			// 点击修改按钮
			exchangems(row) {
				this.tableData.forEach((item, index) => {
					if (row.rotation_id == item.rotation_id) {
						if (item.blem) {
							item.blem = false
						} else {
							this.utils.post({
								url: 'rotationupdate',
								data: {
									rotation_id: row.rotation_id,
									rotation_rou: row.rotation_rou,
									rotation_net_id: row.rotation_net_id
								}
							}).then(res => {
								item.blem = true
							})
						}
					}
				})

			},
			init() {
				this.utils.post({
					url: 'recommend'
				}).then(res => {
					res.data.forEach((item, index) => {
						item.blem = true
						item.srcList = ['' + item.rotation_img]
						item.firl = []
					})
					this.tableData = res.data
				})
			},
			// 获取那六个
			inits() {
				this.utils.post({
					url: 'recommendrou'
				}).then(res => {
					let a = res.data.data
					a.forEach((item,index)=>{
						item.blem = true
						item.options = []
						item.value = ''
						item.srcList = [ '' + item.picture]
					})
					this.tableData_goods = a
				})
			},
			
			// 商品今日推荐点击了确认以后
			exchgoodssome(row){
				
				
				this.tableData_goods.forEach((item,index)=>{
					if( row.goods_id == item.goods_id ){
						if(item.blem){
							item.blem = false;
						}else{
							this.utils.post({
								url:'yesorno',
								data:{
									goods_id:this.goods_id,
									mt_goods_id:row.goods_id
								}
							}).then(res=>{
								item.blem = true
							})
						}	
					}
				})
			},
			
			// 商品今日推荐搜索店铺
			searchto(row){
				this.tableData_goods.forEach((item,index)=>{
					if(item.goods_id == row.goods_id){
						this.utils.post({
							url:'modification',
							data:{
								shop_name:item.shop_name
							}
						}).then(res=>{
							item.goods_name = '请选择商品'
							var va = []
							res.data.forEach((atm,ind)=>{
								 va[ind] = {
									 label:atm.goods_name,
									 value:atm.goods_id
								 } 
								 
								
								 
							})
							this.rowsa = index
							item.options = va
							this.my_some = res.data
							// item.picture =  
						})
					}
				})

			},
			// 选中商品时候触发的按钮
			mychange(e){
				console.log(e)
				this.goods_id = e
				this.my_some.forEach((item,index)=>{
					if(item.goods_id == e){
						this.tableData_goods[this.rowsa].picture = item.picture
						this.tableData_goods[this.rowsa].price = item.price
					}
				})
			},
			// 点击选项卡
			handleClick(tab, event) {
			       
			}
		},
		mounted() {
			this.init()
			this.inits()
			this.a = this.global.imgurl
		}
	}
</script>

<style lang="scss">
	.any-flex {
		justify-content: flex-start;
	}

	.someurl {
		justify-content: space-between;
	}

	.tuijw {
		margin: 10px 0;
	}

	.lbss {
		// text-align: center;
		color: #aa69f9;
		margin-top: 10px;
		// margin-left: 15px;
	}
</style>
