<template>
	<div class="enter">
		<el-collapse v-model="activeNames" @change="handleChange">
			<el-collapse-item v-for='(item,index) in shop_list' :key='index' :title="item.shop_contacts + '----' + item.shop_phone + '----' + '商家入驻申请'" :name="index">
				
				<div class="any-flex">
					<div>
						<i class='active'>申请人:</i> {{item.shop_contacts}}
					</div>

					<div>
						<i  class='active'>手机号:</i> {{item.shop_phone}}
					</div>
					
					<div>
						<i class='active'>店铺名称:</i> {{item.shop_name}}
					</div>
					
					<div>
						<div class="any-flex">
							<div  class='active'>店铺位置:</div>
							<div>{{item.shop_address_provice}}</div>
							<div>{{item.shop_address_city}}</div>
							<div>{{item.shop_address_area}}</div>
							<div>{{item.shop_address_detail}}</div>
						</div>
					</div>
					<div>   <i class='active'>主营项目:</i>  {{item.t_name}} </div>
				</div>

				<div class="any-flex">
					<div style="width: 80%;"></div>
					<el-button type="success" @click='goruns(item)'> 审核 </el-button>
					
				</div>

			</el-collapse-item>

		</el-collapse>

		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" 
			@current-change='changepages'
			:page-count='currentPage'
			:page-size='pagesize'
			>
			</el-pagination>
		</div>


	</div>
</template>

<script>
	export default {
		data() {
			return {
				activeNames:['1'],
				currentPage: 1,
				total:0,
				pagesize:0,
				shop_list:[]
			};
		},
		methods: {
			handleChange(val) {
				console.log(val);
			},
			init(){
				this.utils.post({
					url:'businesssettled'
				}).then(res=>{
					this.shop_list = res.data 
					this.total = res.total
					this.pagesize = res.per_page
					
				})
			},
			changepages(e){
				this.utils.post({
					url:'businesssettled',
					data:{
						page:e
					}
				}).then(res=>{
					this.shop_list = res.data 
					
				})
			},
			goruns(e){
				let shop_mes = {
					shop_address_area: e.shop_address_area,
					shop_address_city: e.shop_address_city,
					shop_address_detail: e.shop_address_detail,
					shop_address_provice: e.shop_address_provice,
					shop_contacts: e.shop_contacts,
					shop_id: e.shop_id,
					shop_name: e.shop_name,
					shop_phone: e.shop_phone,
					t_name: e.t_name
				}
				
				this.$router.push({
				　　path: '/hellow/addenter',
				　　query: {
				　　　　name:shop_mes,
				　　}
				});
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.any-flex{
		justify-content: space-around;
		margin-top: 10px;
	}
	.active{
		color:#40b102;
	}
	
</style>
